import React, {Component} from "react";
import axios from 'axios';
import { Input,Collapse } from 'reactstrap';
import { Card, CardImg, CardBody, Button} from 'reactstrap';

import moment from 'moment';

export default class CardRichiestaAdmin extends Component {
  
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleConferma = this.handleConferma.bind(this);
    this.toggle=this.toggle.bind(this);
    this.state={
      motivazioni: '',
      collapse: false
    }
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  handleConferma = e => {
    e.preventDefault();
    axios
    .post('http://localhost:3000/accettaRichiestaHost', null, {
        params: {
            idUtente: this.props.idUtente,
            email: this.props.email,
        }
    }, {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Accept': 'application/json'
        }
    })
    .then(res => {
        console.log(res);  
        console.log("ciao");
        if (res.data.success === true) {
            alert("Richiesta accettata!");
            window.location.reload(false);
        }
    });
  }

  handleDeclina = e => {
    e.preventDefault();
    axios
    .post('http://localhost:3000/declinaRichiestaHost', null, {
        params: {
            idUtente: this.props.idUtente,
            email: this.props.email,
            motivazioni: this.state.motivazioni,
        }
    }, {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Accept': 'application/json'
        }
    })
    .then(res => {
        console.log(res);  
        
        if (res.data.success === true) {
            alert("Richiesta declinata!");
            window.location.reload(false);
        }
    });
  }

  handleChange =input => e => {
    this.setState({[input]:e.target.value});
  }

  render() {
    
    const enableElimina = this.state.motivazioni.length > 0;
    
    return (
      <div>
        
        <Card  style={{borderRadius:'0'}}  toggler="#toggler"  id={this.props.count}>       
          <CardImg 
            src={'http://localhost:3000/' + this.props.percorsoImmagine} 
            alt="Card image cap" 
          />
          
          <CardBody>
            <center>
              <h3>{this.props.nome} {''} {this.props.cognome}</h3>
            
              <Button 
                color="danger" 
                onClick={this.toggle} 
                style={{ marginBottom: '1rem' }}>
                  Visualizza richiesta
              </Button>
            </center>

          </CardBody>
            
            {/*collapse che fa spuntare il campo di testo per la motivazione.*/}
            <Collapse  isOpen={this.state.collapse}  style={{padding:'3%'}}>
                <Card style={{borderColor:'rgb(220, 53, 69)'}} >
                  <center>
                    <CardBody>
                      <b>Nome e cognome</b> <br />
                      {this.props.nome} {this.props.cognome}
                      <hr />
                      <b>Data di nascita</b> <br />
                      {moment(this.props.dataNascita.split("T")[0], "YYYY-MM-DD").format("DD/MM/YYYY")}
                      <hr />
                      <b>Codice Fiscale</b> <br />
                      {this.props.codiceFiscale} 
                      <hr />
                      <b>Tipo documento</b> <br />
                      {this.props.tipoDocumento} 
                      <hr />
                      <a 
                          href={'http://localhost:3000/' + this.props.percorsoDocumento} 
                          style={{color:'rgb(220, 53, 69)'}}
                          target="_blank" rel="noopener noreferrer" 
                      > 
                        Visualizza documento 
                      </a> 
                      
                      <br/><br/>
                      
                      <Input 
                        type="textarea" 
                        name="motivazioni" 
                        placeholder="Inserisci obbligatoriamente delle motivazini per un eventuale declino"
                        rows={5}
                        onChange={this.handleChange('motivazioni')}
                      />

                      <br/>
                      
                      <center>
                          <Button color="danger" onClick={this.handleConferma} >Accetta</Button>{' '}
                          <Button color="secondary" disabled={!enableElimina} onClick={this.handleDeclina}>Declina</Button>
                      </center>

                    </CardBody>
                  </center>
                </Card>
            </Collapse>

        </Card>

      </div> 
    )
  }
}
