import React, { Component } from "react";
import axios from "axios";
import {
  Col,
  Row,
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  Jumbotron,
  Container,
  FormText,
} from "reactstrap";

import history from "../../history";
import Navbar from "../../components/ComponentiGenerici/Navbar";
import FooterFixed from "../../components/ComponentiGenerici/FooterFixed";

class ModificaProfilo extends Component {
  constructor(props) {
    super(props);

    this.onChangeHandler = this.onChangeHandler.bind(this);

    this.state = {
      telefono: "",
      percorso: "",
      file: "",
    };
  }

  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  onChangeHandler = (event) => {
    this.setState({
      file: event.target.files[0],
      loaded: 0,
    });

    console.log(this.state.file);
  };

  /* richiesta axios per l'aggiornamento dei dati */
  continue = (e) => {
    e.preventDefault();

    console.log("FILE", this.state.file)

    const formData = new FormData();
    formData.append("sessionData", localStorage.getItem("sessionValues"));
    formData.append("avatarUtente", this.state.file);
    formData.append("telefono", this.state.telefono);

    axios
      .post("http://localhost:3000/modificaProfilo", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          Accept: "application/json",
        },
      })
      .then((res) => {
        console.log(res);
        console.log(res.data);

        if (res.data.success === true) {
          alert("Dati modificati con successo!");
          history.push("/area_riservata");
          window.location.reload(false);
        }
      });
  };

  /* richiesta axios per prelevare le informazioni dell'utente già esistenti */
  componentDidMount() {
    
    //se non vi è una sessione per l'utente, lo reindirizzo al login
    if (localStorage.getItem("sessionValues") === null) {
      history.push("/login");
      window.location.reload(false);
    } else {
      axios
        .get(
          "http://localhost:3000/richiediDatiUtente",
          {
            params: {
              sessionData: localStorage.getItem("sessionValues"),
            },
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              Accept: "application/json",
            },
          }
        )
        .then((res) => {
          console.log(res);

          if (res.data.success === true) {
            this.setState({
              telefono: JSON.parse(res.data.data).telefono,
              percorso: res.data.percorsoAvatar,
            });
          }
        });
    }
  }

  render() {

    return (
      <div>
        <Navbar />

        <div style={{borderBottom:'solid', borderTop:'solid', backgroundColor: "white", borderColor:'#AAAAAA'}}>
          <center>
          <h2 class="glacialReg"  style={{padding:'1%',marginBottom:'0'}} >
            MODIFICA PROFILO
          </h2>
          </center>
        </div>

        <div
          class="row h-100 justify-content-md-center"
          style={{ margin: "5%" }}
        >
          <div class="col-sm-12 col-md-8 col-lg-6 my-auto">
            <Jumbotron>
              <Container fluid>
                <center>
                  <img
                    src={"http://localhost:3000/" + this.state.percorso}
                    height="auto"
                    width="256px"
                    alt="Errore"
                  />
                </center>
                <Form>
                  <Row form>
                    <Col>
                      <FormGroup>
                        <Label for="exampleEmail">Telefono</Label>
                        <Input
                          type="text"
                          name="telefono"
                          id="exampleEmail"
                          placeholder="Inserisci il tuo numero di telefono"
                          defaultValue={this.state.telefono}
                          onChange={this.handleChange("telefono")}
                        />
                      </FormGroup>
                    </Col>
                  </Row>

                  <br />

                  <Row form>
                    <Col>
                      <FormGroup row>
                        <Input
                          type="file"
                          name="foto"
                          accept="image/*"
                          onChange={this.onChangeHandler}
                        />
                        <FormText color="muted">
                          Inserisci una nuova foto profilo
                        </FormText>
                      </FormGroup>
                    </Col>
                  </Row>

                  <center>
                    <Button color="danger" onClick={this.continue}>
                      Modifica
                    </Button>
                  </center>
                </Form>
              </Container>
            </Jumbotron>
          </div>
        </div>

        <FooterFixed />
      </div>
    );
  }
}

export default ModificaProfilo;
