import React, { Component } from "react";
import axios from "axios";

import CardRichiestaAdmin from '../../components/Card/CardRichiestaAdmin'
import NavbarAdmin from "../../components/ComponentiGenerici/NavbarAdmin";
import Footer from "../../components/ComponentiGenerici/Footer";

import history from '../../history';

export default class PannelloControlloAdmin extends Component {
  state = {
    user: '',
    richieste: [/*
            {   
                percorsoImmagine: 'https://www.publicdomainpictures.net/pictures/270000/velka/avatar-people-person-business-.jpg',
                nome: 'Alfio',
                cognome: 'Gianni',
                dataNascita: '20/10/98',
                telefono: '328919515',
                tipoDocumento: 'A',
                codiceFiscale: 'SSDDSASDADS',
                scansioneDocumento: 'http://calvino.polito.it/~terzafac/Corsi/analisi1/pdf/funzioni-svolti.pdf',
                motivazioneDeclino: '',
                idUtente: '10'
            
            },
            {   
              percorsoImmagine: 'https://www.publicdomainpictures.net/pictures/270000/velka/avatar-people-person-business-.jpg',
              nome: 'Alfio2',
              cognome: 'Gianni2',
              dataNascita: '20/10/98',
              telefono: '328919515',
              tipoDocumento: 'A',
              codiceFiscale: 'SSDDSASDADS',
              scansioneDocumento: 'http://calvino.polito.it/~terzafac/Corsi/analisi1/pdf/funzioni-svolti.pdf',
              motivazioneDeclino: '',
              idUtente: '11'
          
          },

          {   
            percorsoImmagine: 'https://www.publicdomainpictures.net/pictures/270000/velka/avatar-people-person-business-.jpg',
            nome: 'Alfio3',
            cognome: 'Gianni3',
            dataNascita: '20/10/98',
            telefono: '328919515',
            tipoDocumento: 'A',
            codiceFiscale: 'SSDDSASDADS',
            scansioneDocumento: 'http://calvino.polito.it/~terzafac/Corsi/analisi1/pdf/funzioni-svolti.pdf',
            motivazioneDeclino: '',
            idUtente: '12'
        }
      */],
  };

  componentDidMount() {

    if ( localStorage.getItem('id') === null ) {
        history.push("/admin");
        window.location.reload(false);
    } else {
        axios
          .get('http://localhost:3000/richiesteHost', {
            params: {
              idAdmin: localStorage.getItem('id')
            }
          }).then(res => {
              console.log(res);
              if (res.data.richieste === undefined) {
                this.setState({richieste: null });
              } else {
                this.setState({richieste: JSON.parse(res.data.richieste)});
                console.log(this.state.richieste);
              }
              
          });
    }
  }


  render () {

    const renderRichiestaCondizionale = () => {
            
      if( this.state.richieste != null ) {
        return  this.state.richieste.map(richiesta => 
                  <div class="col-12 col-sm-12 col-sm-12 col-md-6 col-lg-6 my-auto mgT">
                      <CardRichiestaAdmin 
                          percorsoImmagine = {richiesta.percorsoFoto}
                          percorsoDocumento = {richiesta.percorsoDocs}
                          nome = {richiesta.nome}
                          cognome = {richiesta.cognome}
                          dataNascita = {richiesta.dataNascita}
                          telefono = {richiesta.telefono}
                          codiceFiscale = {richiesta.codiceFiscale}
                          restrizioni = {richiesta.restrizioni}
                          tipoDocumento = {richiesta.tipoDocumento}
                          scansioneDocumento = {richiesta.scansioneDocumento}
                          idUtente = {richiesta.ID_Utente}
                          email = {richiesta.email}
                      />
                  </div>
                );
      } 
      return <center>
                  <div>
                      <img src="https://image.flaticon.com/icons/svg/3029/3029232.svg" height="64px" width="64px" alt="Errore"/><br /><br />
                      <h4 class="glacialReg">
                          Sembra che non ci siano delle richieste pendenti, guardati un epidodio della tua serie preferita e torna tra un po'!
                      </h4>
                  </div>
              </center>;
  }


    return(
      <div>
          
          <NavbarAdmin />
          
          <div style={{minHeight:"79vh"}}>

            <div class="cont-hr">
              <center><h1 class="glacialReg">RICHIESTE PENDENTI</h1></center>
            </div>
            
            <div class="container" style={{padding:'0 2% 2% 2%'}}>
              <div class="row">
                 {renderRichiestaCondizionale()}
             </div>    
            </div>   

          </div>

        <Footer />
      </div>
    );
  }
}
