import React, { Component } from "react";

import axios from "axios";
import { AvForm, AvField, AvGroup, AvInput, AvFeedback, AvRadioGroup, AvRadio} from "availity-reactstrap-validation";
import { Button, FormGroup, Label, Card, CardText, Input} from "reactstrap";

import moment from 'moment';
import history from '../../history';

export default class Login extends Component {
  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  state = {
    immobileGenerico: [],
    immobileSpecifico: [],
    ospiti: [],
    documenti: [],
    numeroCamereSingole: 0,
    numeroCamereDoppie: 0,
    numeroCamereTriple: 0,
    numeroCamereQuadruple: 0,
    numeroCamereExtra: 0,
    modPagamento: '',
    foto: '',
    saldoTotale: 15,
    numeroOspiti: '',
    dataArr: '',
    dataFin: '',
    periodo: '',
    numeroEsenti: '',
    modalitaPagamentoTasse: 1,
    curDate: '',
    minCheckOutDate: '',
  };

  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);

    this.state.curDate = moment().format('YYYY-MM-DD');
    this.state.minCheckOutDate = moment().format('YYYY-MM-DD');
  }

  // gestione cambiamenti
  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
    
    if (input === "dataArr") {
      this.setState({
        minCheckOutDate: (e.target.value)
      })
    }

      // aggiorno il periodo  
      var dataF = moment(this.state.dataFin).valueOf(); 
      var dataA = moment(this.state.dataArr).valueOf(); 
      
      const diffTime = Math.abs(dataF - dataA);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      
      this.setState({
        periodo: Math.max(1, diffDays),
      });
  
      // aggiorno il prezzo
      this.state.saldoTotale = ( this.state.immobileSpecifico.prezzoSingole * this.state.numeroCamereSingole + 
                                this.state.immobileSpecifico.prezzoDoppie * this.state.numeroCamereDoppie +
                                this.state.immobileSpecifico.prezzoTriple * this.state.numeroCamereTriple + 
                                this.state.immobileSpecifico.prezzoQuadruple * this.state.numeroCamereQuadruple +
                                this.state.immobileSpecifico.prezzoExtra * this.state.numeroCamereExtra +
                                this.state.immobileGenerico.tasseSoggiorno * (this.state.numeroOspiti - this.state.numeroEsenti) 
                                * this.state.modalitaPagamentoTasse ) * this.state.periodo;              
  };

  onChangeHandler = (event) => {
    console.log(event.target.files[0]);
    this.setState({
      foto: event.target.files[0],
      loaded: 0,
    });
  };


  handleChangeDocument(i, event) {

    let ospiti = [...this.state.ospiti];

    if (event.target.name === "nome") {
        ospiti[i].nome = event.target.value;
    } else if (event.target.name === "cognome") {
        ospiti[i].cognome = event.target.value;
    } else if (event.target.name === "email") {
        ospiti[i].email = event.target.value;
    } else if (event.target.name === "documento") {
      
        ospiti[i].documento = event.target.files[0].name;
        let documenti = [...this.state.documenti];
        documenti[i] = event.target.files[0];
        this.setState({documenti: documenti});

    } else if (event.target.name === "dataNascita") {
        ospiti[i].dataNascita = event.target.value;
    }
    
    this.setState({ ospiti: ospiti });

    console.log("OSPITI: ",this.state.ospiti);
    console.log("DOCUMENTI: ", this.state.documenti);

  }

  // aggiunta di un ospite
  addClick(){
    this.setState(prevState => ({ ospiti: [...prevState.ospiti, {nome:'', cognome:'', email:'', documento: '', dataNascita:''}]}));
    this.setState(prevState => ({ documenti: [...prevState.documenti, '']}));
  }
  

  // rimozione di un ospite
  removeClick(i){
     let ospiti = [...this.state.ospiti];
     let documenti = [...this.state.documenti];
     ospiti.splice(i,1);
     documenti.splice(i,1);
     this.setState({ ospiti: ospiti, documenti: documenti});

     console.log("OSPITI: ",this.state.ospiti);
     console.log("DOCUMENTI: ", this.state.documenti);
  }

  // creazione dell'interfaccia dinamica
  createUI(){
    
    return this.state.ospiti.map((el, i) => 
        <div key={i}>
          <div class="row">
                    <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          name="nome"
                          label="Nome"
                          type="text"
                          onChange={this.handleChangeDocument.bind(this, i)}
                        />
                      </AvGroup>
                    </div>

                    <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          name="cognome"
                          label="Cognome"
                          type="text"
                          onChange={this.handleChangeDocument.bind(this, i)}
                        />
                      </AvGroup>
                    </div>
                  </div>

                  <div class="row">
                    <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          name="email"
                          label="Email"
                          type="email"
                          onChange={this.handleChangeDocument.bind(this, i)}
                        />
                      </AvGroup>
                    </div>

                    <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          label="Data di nascita"
                          name="dataNascita"
                          type="date"
                          min={this.state.curDate}
                          onChange={this.handleChangeDocument.bind(this, i)}
                        />
                        <AvFeedback>Campo obbligatorio!</AvFeedback>
                      </AvGroup>
                    </div>
                  
                  </div>

                  <div class="row">
                    <div class="col-10 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          name="documento"
                          label="Scansione documento"
                          type="file"
                          onChange={this.handleChangeDocument.bind(this, i)}
                        />
                      </AvGroup>
                    </div>
                 
                    <div class="col-2 my-auto griglia-casa">
                        <Button color="danger" onClick={this.removeClick.bind(this, i)}>X</Button>
                    </div>
                  </div>
                  <hr />
              {/*<input type="text" value={el||''} onChange={this.handleChangeDocument.bind(this, i)} />*/}
         
        </div>          
    )
  }

  // richiesta di prenotazione
  handleSubmit = event => {
    event.preventDefault();
    
    const formData = new FormData();
    formData.append('sessionData', localStorage.getItem("sessionValues"));
    formData.append('idImmobile', this.props.idImmobile);
    formData.append('checkin',this.state.dataArr);
    formData.append('checkout',this.state.dataFin);
    formData.append('saldoTotale',this.state.saldoTotale);
    formData.append('tasseOnline', this.state.modalitaPagamentoTasse === 1);
    formData.append('numeroEsenti', this.state.numeroEsenti);
    formData.append('listaOspiti', JSON.stringify(this.state.ospiti));
    formData.append('numeroCamereSingole', this.state.numeroCamereSingole);
    formData.append('numeroCamereDoppie', this.state.numeroCamereDoppie);
    formData.append('numeroCamereTriple', this.state.numeroCamereTriple);
    formData.append('numeroCamereQuadruple', this.state.numeroCamereQuadruple);
    formData.append('numeroCamereExtra', this.state.numeroCamereExtra);

    this.state.documenti.forEach(file=>{
      formData.append("documentiOspiti",file);
    })
    
    axios
    .post('http://localhost:3000/prenotazioneBnB', formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
            'Accept': 'application/json'
        }
    })
    .then(res => {
        console.log(res.data);
        if ( res.data.success === true) {
            alert("Prenotazione richiesta!");
            history.push("/area_riservata");
            window.location.reload(false);
        } else {
            alert(res.data.message);
        }
    });
}
  

 componentDidMount() {
    //se non vi è una sessione per l'utente, lo reindirizzo al login
    if (localStorage.getItem("sessionValues") === null) {
      history.push("/login");
      window.location.reload(false);
    } else {

        axios
          .get(
            "http://localhost:3000/visualizzaImmobile",
            {
              params: {
                idImmobile: this.props.idImmobile
              },
            },
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                "Accept": "application/json",
              },
            }
          )
          .then((res) => {
            console.log(res);

            if (res.data.success === true) {
              console.log(res.data)
              this.setState({
                immobileGenerico: JSON.parse(res.data.datiImmobile),
                immobileSpecifico: JSON.parse(res.data.datiSpecifici),
                foto: res.data.percorsoFoto
              })

              if (this.state.immobileGenerico.modalitaPagamento === "online") {
                this.setState({
                  modPagamento: "PAGA 100% ONLINE" 
                })
              } else if (this.state.immobileGenerico.modalitaPagamento === "metà in loco") {
                this.setState({
                  modPagamento: "PAGA 50% ORA E 50% IN LOCO" 
                })
              } else if (this.state.immobileGenerico.modalitaPagamento === "dilazionato") {
                this.setState({
                  modPagamento: "PAGA 50% ORA E 50% TRA UN PO' (SEMPRE ONLINE XD)" 
                })
              }
              
            }

          });
    }
  }

  render() {

    /* 
        renderCondizioneli -> stampo la riga relativa ad un tipo di stanza esclusivamente se 
        la struttura ne disponde almeno uno 
    */
   const renderCondizionaleSingole = () => {

    if (this.state.immobileSpecifico.numeroSingole > 0) {
      return(<div class="row">
              <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                <AvGroup>
                  <AvField
                    name="numeroCamereSingole"
                    label="Numero camere singole"
                    type="number"
                    onChange={this.handleChange("numeroCamereSingole")}
                  />
                </AvGroup>
              </div>

              <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                <AvGroup>
                  <AvField
                    name="prezzoCamereSingole"
                    label="Prezzo totale singole"
                    type="text"
                    disabled
                    value={this.state.numeroCamereSingole * this.state.immobileSpecifico.prezzoSingole}
                  />
                </AvGroup>
              </div>
            </div>);
    }
  
   } 

    const renderCondizionaleDoppie = () => {
      if (this.state.immobileSpecifico.numeroDoppie > 0) {
          return(
            <div class="row">
            <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
              <AvGroup>
                <AvField
                  name="numeroCamereDoppie"
                  label="Numero camere doppie"
                  type="number"
                  onChange={this.handleChange("numeroCamereDoppie")}
                />
              </AvGroup>
            </div>

            <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
              <AvGroup>
                <AvField
                  name="prezzoCamereDoppie"
                  label="Prezzo totale doppie"
                  type="text"
                  disabled
                  value={this.state.numeroCamereDoppie * this.state.immobileSpecifico.prezzoDoppie}
                />
              </AvGroup>
            </div>
          </div>
          );
      }
    }

    const renderCondizionaleTriple = () => {
      if (this.state.immobileSpecifico.numeroTriple > 0) {
      
        return ( <div class="row">
                  <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                    <AvGroup>
                      <AvField
                        name="numeroCamereTriple"
                        label="Numero camere triple"
                        type="number"
                        onChange={this.handleChange("numeroCamereTriple")}
                      />
                    </AvGroup>
                  </div>

                  <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                    <AvGroup>
                      <AvField
                        name="prezzoCamereTriple"
                        label="Prezzo per camera"
                        type="text"
                        disabled
                        value={this.state.numeroCamereTriple * this.state.immobileSpecifico.prezzoTriple}
                      />
                    </AvGroup>
                  </div>
                </div>);
      }
    }

    const renderCondizionaleQuadruple = () => {

      if (this.state.immobileSpecifico.numeroQuadruple > 0) {
        return ( <div class="row">
                    <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          name="numeroCamereQuadruple"
                          label="Numero camere quadruple"
                          type="number"
                          onChange={this.handleChange("numeroCamereQuadruple")}
                        />
                      </AvGroup>
                    </div>

                    <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          name="prezzoCamereQuadruple"
                          label="Prezzo totale quadruple"
                          type="text"
                          disabled
                          value={this.state.numeroCamereQuadruple * this.state.immobileSpecifico.prezzoQuadruple}
                        />
                      </AvGroup>
                    </div>
                  </div>);
      }
    }

    const renderCondizionaleExtra = () => {
      
      if (this.state.immobileSpecifico.numeroExtra > 0) {
        return (<div class="row">
                  <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                    <AvGroup>
                      <AvField
                        name="numeroCamereExtra"
                        label={"Numero camere per "+this.state.immobileSpecifico.personeCameraExtra}
                        type="number"
                        onChange={this.handleChange("numeroCamereExtra")}
                      />
                    </AvGroup>
                  </div>

                  <div class="col-12 col-sm-12 col-md-6 col-lg-6 my-auto griglia-casa">
                    <AvGroup>
                      <AvField
                        name="prezzoCamereExtra"
                        label={"Prezzo totale per la camera per "+this.state.immobileSpecifico.personeCameraExtra}
                        type="text"
                        disabled
                        value={this.state.numeroCamereExtra * this.state.immobileSpecifico.prezzoExtra}
                      />
                    </AvGroup>
                  </div>
                </div>);
      }
    }

    /* 
      disabilito il bottone per aggiungere gli ospiti se la dimensione dell'array supera il numero di ospiti inserito 
    */
    const disableButton = this.state.ospiti.length ===  this.state.numeroOspiti;


    return (
        
        <div class="ez">
          <div class="header-row">
            <Card body outline color="secondary">
              <CardText>
                <justify>
                  <center>
                    <p class="h1 glacialReg" style={{color: "#dc3545"}} >
                      {this.state.immobileGenerico.titolo}
                    </p>
                  </center>
                </justify>
              </CardText>
            </Card>
          </div>
          <div class="container my-auto">
            <img
              src={"http://localhost:3000/"+this.state.foto}
              width="100%"
              maxWidth="500px"
              height="auto"
              alt="Errore"
            />
          </div>
          <div>
            <div class="container my-auto">
              <AvForm>
                {/* CARD DATE */}
                <Card body outline color="secondary" style={{borderRadius:'0'}}>
                  <center>
                    <img src="https://image.flaticon.com/icons/svg/3108/3108354.svg" alt="Errore" height="64px" width="64px" />
                    <h1 class="glacialReg">
                      Conferma i dati
                    </h1>
                  </center>

                  <br />

                  <CardText>
                    <div class="row">
                      <div class="col-12 col-sm-12 col-md-3 col-lg-3 my-auto griglia-casa">
                        <center>
                          <Label for="example">
                            <p class="testo-griglia">Checkin</p>
                          </Label>
                        </center>
                      </div>
                      <div class="col-12 col-sm-12 col-md-9 col-lg-9">
                        <AvGroup>
                          <AvInput
                            name="dataArr"
                            type="date"
                            onChange={this.handleChange("dataArr")}
                            min={this.state.curDate}
                            required
                          />
                          <AvFeedback>Campo obbligatorio!</AvFeedback>
                        </AvGroup>
                      </div>
                    </div>

                    <div class="row">
                      <div class="col-12 col-sm-12 col-md-3 col-lg-3 my-auto griglia-casa">
                        <center>
                          <Label for="example">
                            <p class="testo-griglia">Checkout</p>
                          </Label>
                        </center>
                      </div>
                      <div class="col-12 col-sm-12 col-md-9 col-lg-9">
                        <AvGroup>
                          <AvInput
                            type="date"
                            name="dataFin"
                            onChange={this.handleChange("dataFin")}
                            min={this.state.minCheckOutDate} 
                            required
                          />
                          <AvFeedback>Campo obbligatorio!</AvFeedback>
                        </AvGroup>
                      </div>
                    </div>

                    <div class="row" style={{ marginTop: "5px" }}>
                      <div class="col-12 col-sm-12 col-md-3 col-lg-3 my-auto griglia-casa">
                        <center>
                          <Label for="example">
                            <p class="testo-griglia">Numero di ospiti</p>
                          </Label>
                        </center>
                      </div>
                      <div class="col-12 col-sm-12 col-md-9 col-lg-9">
                        <AvGroup>
                          <AvInput
                            type="number"
                            name="numeroOspiti"
                            min={1}
                            placeholder="Inserisci il numero degli ospiti"
                            onChange={this.handleChange("numeroOspiti")}
                            required
                          />
                          <AvFeedback>Campo obbligatorio!</AvFeedback>
                        </AvGroup>
                      </div>
                    </div>
                  </CardText>
                </Card>

                <br />

                {/* CARD CAMERE */}
                <Card body outline color="secondary" style={{borderRadius:'0'}} >
                  <center>
                    <img src="https://image.flaticon.com/icons/svg/3094/3094080.svg" alt="Errore" height="64px" width="64px" />
                    <h1 class="glacialReg">
                      Seleziona il numero di stanze
                    </h1>
                  </center>

                  <br />

                  <CardText>
                    
                    {renderCondizionaleSingole()}

                    {renderCondizionaleDoppie()}

                    {renderCondizionaleTriple()}

                    {renderCondizionaleQuadruple()}

                    {renderCondizionaleExtra()}

                  </CardText>
                </Card>

                <br />

                
                <Card body outline color="secondary" style={{borderRadius:'0'}}>
                  <center>
                    <img src="https://image.flaticon.com/icons/svg/3093/3093188.svg" alt="Errore" height="64px" width="64px" />
                    <h1 class="glacialReg">
                      Inserisci le informazioni degli ospiti
                    </h1>
                  </center>

                  <br />

                  <CardText>
                    {this.createUI()}
                    <center>
                      <Button 
                        color="outline-danger" 
                        onClick={this.addClick.bind(this)} 
                        style={{marginTop:'2%'}}
                        disabled={disableButton}
                      >
                        Aggiungi ospite
                      </Button>
                    </center>
                  </CardText>
                </Card>

                <br />

                {/* INFORMAZIONI SUL PAGAMENTO */}
                <Card body outline color="secondary" style={{borderRadius:'0'}}>
                  <center>
                    <img src="https://image.flaticon.com/icons/svg/3079/3079002.svg" alt="Errore" width="64px" height="64px" />
                    <h1 class="glacialReg">
                      Informazioni sul pagamento
                    </h1>
                  </center>
                  <br />
                  <CardText>
                    <center><h3 class="glacialReg">Modalità di pagamento</h3></center>
                    <Input type="textarea" name="text" id="exampleText" value={this.state.modPagamento} disabled rows={5} /><br />
                    
                    <center>
                        <h3 class="glacialReg">Tasse di soggiorno</h3>
                    </center>
                    
                    <div class="container">
                      <div class="row">
                        <div class="col-12 col-md-6">
                            <Label><h5 class="glacialReg">Esenti</h5></Label>
                            <Input type="textarea" title="Esenti dalle tasse" value={this.state.immobileGenerico.esentiTasseSoggiorno} disabled rows={5} /><br />
                        </div>

                        <div class="col-12 col-md-6">
                            <Label ><h5 class="glacialReg">Costo giornaliero per persona: {this.state.immobileGenerico.tasseSoggiorno}€</h5></Label>
                            <hr />
                            <AvGroup>
                              <AvField
                                name="numeroEsenti"
                                label="Indica il numero di esenti dalle tasse di soggiorno"
                                type="number"
                                min={0}
                                max = {this.state.numeroOspiti}
                                onChange={this.handleChange("numeroEsenti")}
                              />
                            </AvGroup>
                        </div>

                        <div class="col-12 text-center">
                          <AvGroup>
                              <Label ><h5 class="glacialReg">Dove vuoi pagare le tasse?</h5></Label>
                              <AvRadioGroup inline name="modalitaPagamentoTasse" onClick={this.handleChange('modalitaPagamentoTasse')}>
                                <AvRadio customInput label="Online" value="1" />
                                <AvRadio customInput label="In loco" value="0" />
                              </AvRadioGroup>
                          </AvGroup>
                        </div>
                      </div>
                    </div>

                    <hr />

                    <center><h3>Totale: {this.state.saldoTotale}€ </h3></center>
                  </CardText>
                </Card>

                <br />

                <FormGroup>
                  <center>
                    <Button color="danger" size="lg" onClick={this.handleSubmit}>
                      Prenota
                    </Button>
                  </center>
                </FormGroup>
              </AvForm>
            </div>
          </div>
        </div>
    );
  }
}
