import React from "react";
import { Bar } from "react-chartjs-2";

export default class TestChart extends React.Component {

  state = {
    labels: [
      "Gennaio",
      "Febbraio",
      "Marzo",
      "Aprile",
      "Maggio",
      "Giugno",
      "Luglio",
      "Agosto",
      "Settembre",
      "Ottobre",
      "Novembre",
      "Dicembre",
    ],
    datasets: [
      {
        label: "Guadagni ottenuti e stimati",
        backgroundColor: "rgb(220, 53, 69)",
        borderColor: "rgba(0,0,0,1)",
        borderWidth: 2,
        data: [],
      },
    ],
  };

  render() {

    this.state.datasets[0].data = this.props.datiMensili;

    return (
      <div>
        
        <Bar
          data={this.state}
          options={{
            title: {
              display: true,
              fontSize: 20,
            },
            legend: {
              display: true,
              position: "top",
            },
            
          }}
        />
      </div>
    );
  }
}
