import React, { Component } from "react";
import history from '../../history';

import Footer from "../../components/ComponentiGenerici/Footer";
import Navbar from "../../components/ComponentiGenerici/Navbar";
import ModificaBnB from "../../components/GestioneImmobili/ModificaBnB";
import ModificaCasa from "../../components/GestioneImmobili/ModificaCasa";

export default class ModificaImmobile extends Component {
  state = {
    idImmobile: "",
    isBnb: "",
  };

  componentDidMount() {
    
    //se non vi è una sessione per l'utente, lo reindirizzo al login
    if (localStorage.getItem("sessionValues") === null) {
      history.push("/login");
      window.location.reload(false);
    } else {
      this.state.idImmobile = this.props.match.params.id;
      this.state.isBnb = this.props.match.params.tipoImmobile;
    }
    
  }

  render() {
    if (this.props.match.params.tipoImmobile === "true") {
      return (
        <div>
          <Navbar />
          <ModificaBnB idImmobile={this.props.match.params.id} />
          <Footer />
        </div>
      );
    } else {
      return (
        <div>
          <Navbar />
          <ModificaCasa idImmobile={this.props.match.params.id} />
          <Footer />
        </div>
      );
    }
  }
}
