import React, { Component } from "react";
import axios from 'axios';

import FooterFixed from "../../components/ComponentiGenerici/FooterFixed";
import Navbar from "../../components/ComponentiGenerici/Navbar";

import { Jumbotron, Button } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";

import history from '../../history';
import AvFeedback from "availity-reactstrap-validation/lib/AvFeedback";
import AvGroup from "availity-reactstrap-validation/lib/AvGroup";

export default class Pagamento extends Component {
  componentWillMount() {
    document.body.style.backgroundImage = "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  state = {
    
    carta: "",
    cvv: "",
    mese: "",
    anno: "",

    modalitaPagamento: '',
    saldoTotale: '',
    moltiplicatore: '',
  };

  handleChange = input => e => {
    this.setState({[input]: e.target.value});
  }

  /* carico le informazioni relarive al pagamento corrente */
  componentDidMount() {
      /* 
        se non è presente una entry nel localstorage allora  per l'utente allora 
        lo reindirizzo al login 
      */
      if (localStorage.getItem("sessionValues") === null) {
          history.push("/login");
          window.location.reload(false);
      } else {
    
          /* pagamento di una prenotazione */
          if (this.props.match.params.tipo === "prenotazione") {
            
            axios 
              .get(
                "http://localhost:3000/modalitaPagamentoPrenotazione",
                {
                  params: {
                    idPrenotazione: this.props.match.params.id,
                    sessionData: localStorage.getItem("sessionValues")
                  },
                },
                {
                  headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
                    "Accept": "application/json",
                  },
                }
              )
              .then((res) => {
                console.log(res.data);

                if (res.data.success === true) {
                  
                    this.setState({
                      modalitaPagamento: res.data.data.modalitaPagamento,
                      saldoTotale: res.data.data.saldoTotale
                    })
                }

              });
            } 
            
            /* pagamento di un rendiconto */
            else {  
              console.log("COMUNE", this.props.match.params.id)
              axios
              .get(
                "http://localhost:3000/totaleTasseRendiconto",
                {
                  params: {
                    sessionData: localStorage.getItem("sessionValues"),
                    idComune: this.props.match.params.id
                  },
                },
                {
                  headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
                    "Accept": "application/json",
                  },
                }
              )
              .then((res) => {
                console.log("SALDO TOTALE",res.data.totaleTasse);

                if (res.data.success === true) {
                  
                    this.setState({
                      saldoTotale: res.data.totaleTasse
                    })
                }

              });
            }
      }
  } 

  /* richieste axios per memorizzare l'avvenuto pagamento */
  handlePaga = () => {

    if (this.props.match.params.tipo === "prenotazione") {
      axios
        .post(
          "http://localhost:3000/effettuaPagamento", null,
            {
            params: {
              idPrenotazione: this.props.match.params.id,
              sessionData: localStorage.getItem("sessionValues"),
              importo: (this.state.saldoTotale * this.state.moltiplicatore)
            },
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              "Accept": "application/json",
            },
          }
        )
        .then((res) => {
           
          if (res.data.success) {
            history.push("/area_riservata")
            window.location.reload(false);
          } else {
            alert(res.data.message);
          }

        });
    } else {
      axios
        .post(
          "http://localhost:3000/pagamentoRendiconto", null,
            {
            params: {
              idComune: this.props.match.params.id,
              sessionData: localStorage.getItem("sessionValues"),
              importo: this.state.saldoTotale,
            },
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              "Accept": "application/json",
            },
          }
        )
        .then((res) => {
          console.log(res.data);

          if (res.data.success) {
            //history.push("/area_riservata")
            //window.location.reload(false);
          } else {
            alert(res.data.message);
          }

        });
    }
  }


  render() {

    /* 
      Il componente viene utilizzato per paagare sia un rendiconto che 
      una prenotazione, pertanto a seconda del contesto, utilizzo un output
      differente 
    */
    const renderCondizionalePagamento = () => {

      if (this.props.match.params.tipo === "prenotazione") {

        if (this.state.modalitaPagamento === "online") {
            this.state.moltiplicatore = 1; 
        } else {
          this.state.moltiplicatore = 0.5;
        }

        return  <div class="text-center glacialReg">
                    <img src="https://image.flaticon.com/icons/svg/3141/3141971.svg" alt="Errore" height="64px" width="64px" style={{margin:'2%'}}/> 
                    <h3> Totale da pagare: € {this.state.saldoTotale * this.state.moltiplicatore}</h3> <br />
                </div> 
      } else {
        return  <div class="text-center glacialReg">
                    <img src="https://image.flaticon.com/icons/svg/3141/3141971.svg" alt="Errore" height="64px" width="64px" style={{margin:'2%'}}/> 
                    <h3> Totale da pagare: € {this.state.saldoTotale}</h3> <br />
                </div> 
      }
     
    
    }

    /* disabilito il bottone di conferma qualora non siano stati inseriti tutti i campi */
    const enableButton = this.state.carta.length > 0 &&
                         this.state.anno.length > 0 &&
                         this.state.mese.length > 0 &&
                         this.state.cvv.length > 0;

    return (
      <div>
        <Navbar />
        
        <div class="row h-100 justify-content-md-center" style={{ margin: "5%" }}>
          <div class="col-sm-12 col-md-8 col-lg-6 my-auto">
            <Jumbotron>
              <center>{renderCondizionalePagamento()}</center>
              <AvForm>
                
                <AvField
                  name="carta"
                  label="Numero carta di credito"
                  validate={{
                    required: {
                      value: true,
                      errorMessage: "Il campo è richiesto.",
                    },
                    pattern: {
                      value: /^(?:4[0-9]{12}(?:[0-9]{3})?|5[1-5][0-9]{14})$/, 
                      errorMessage:'Non sembra tu abbia inserito una carta di credito'
                    }
                  }}
                  onChange={this.handleChange("carta")}
                />

                <AvField
                  name="mese"
                  label="Mese di scadenza"
                  type="number"
                  min={1}
                  max={12}
                  errorMessage="Campo non valido"
                  onChange={this.handleChange("mese")}
                />

                <AvField
                  name="anno"
                  label="Anno di scadenza"
                  type="number"
                  min={new Date().getFullYear()}
                  errorMessage="Sembra che la tua carta sia già scaduta"
                  onChange={this.handleChange("anno")}
                  validate={{
                    required: {
                      value: true,
                    },
                  }}
                />

                <AvField
                  name="cvv"
                  label="CVV"
                  onChange={this.handleChange("cvv")}
                  validate={{
                    required: {
                      value: true,
                      errorMessage: "Campo non valido",
                    },
                    pattern: {
                      value: /^[0-9]{3}$/, 
                      errorMessage:'Non sembra tu abbia inserito un CVV'
                    }
                  }}
                />

                <hr className="my-2" />
                
                <center class="glacialReg">
                  <Button color="danger" onClick={this.handlePaga} disabled={!enableButton}> CONFERMA </Button>
                </center>
              
              </AvForm>
            </Jumbotron>
          </div>
        </div>


        <FooterFixed />
      </div>
    );
  }
}


