import React from "react";
import axios from "axios";
import { AvForm, AvField, AvGroup, AvInput} from "availity-reactstrap-validation";
import { Button, Label, Card } from "reactstrap";
import moment from 'moment'
import history from '../../history';


class PannelloRiepilogoPrenotazione extends React.Component {
  
  state = {
    
    percorsoFoto: '',
    percorsoArchivio: '',
    datiCamerePrenotate: [],
    datiSpecificiCasa: [],
    datiSpecificiBNB: [],
    datiImmobile: [],
    datiPrenotazione: [],
    listaOspiti: [],
    numOspiti: '',
    ospiti: [],
    motivazioni: '',
      
  };
  

  componentDidMount() {
    
    axios
      .get(
        "http://localhost:3000/riepilogoPrenotazioneHost",
        {
          params: {
            idPrenotazione: this.props.idPrenotazione,
            sessionData: localStorage.getItem("sessionValues"),
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            "Accept": "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);

        if (res.data.success === true) {
          
          this.setState({
            listaOspiti: JSON.parse(res.data.listaOspiti),
            datiImmobile: JSON.parse(res.data.datiImmobile),
            percorsoFoto: res.data.percorsoFotoImmobile,
            percorsoArchivio: res.data.percorsoArchivio,
            datiPrenotazione: JSON.parse(res.data.datiPrenotazione),
          })


          if (res.data.datiImmobile.bnb === 0) {
            this.setState({
                datiSpecificiCasa: JSON.parse(res.data.datiSpecifici),
                datiSpecificiBNB: undefined,
                datiCamerePrenotate: undefined,
            })
          } else {
            this.setState({
              datiSpecificiCasa: undefined,
              datiSpecificiBNB: JSON.parse(res.data.datiSpecifici),
              datiCamerePrenotate: JSON.parse(res.data.datiCamerePrenotate),
            })
          }

          console.log("DATI prenotazione: ", this.state.datiPrenotazione);
          console.log(">>>", moment(this.state.datiPrenotazione.checkin.split("T"), "YYYY-MM-DD").format("DD/MM/YYYY"))
        }

      });
  }
    

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };


  handleAccetta = () => {
    axios
      .post(
        "http://localhost:3000/accettaPrenotazione", null,
        {
          params: {
            idPrenotazione: this.props.idPrenotazione,
            sessionData: localStorage.getItem("sessionValues"),
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            "Accept": "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);

        if (res.data.success) {
          alert("Prenotazione accettata!");
          history.push("/area_riservata");
          window.location.reload(false);
        } else {
          alert(res.data.message);
        }

      });
  }

  handleDeclina = () => {
    axios
      .post(
        "http://localhost:3000/declinaPrenotazione", null,
        {
          params: {
            idPrenotazione: this.props.idPrenotazione,
            sessionData: localStorage.getItem("sessionValues"),
            motivazioni: this.state.motivazioni,
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            "Accept": "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);

        if (res.data.success) {
          alert("Prenotazione rifiutata!");
          history.push("/area_riservata");
          window.location.reload(false);
        } else {
          alert(res.data.message);
        }

      });
  }


  render() {

    const disableDeclina = this.state.motivazioni.length === 0;

    const renderCondizionaleOspiti = () => {
      
      return  this.state.listaOspiti.map(ospite => 
                <div class="container">
                  <div class="row">
                    <div class="col-6 col-lg-6 glacialReg">
                          <Label>Nome</Label>
                          <AvInput 
                              type="text" 
                              name="nome"
                              label="Nome"
                              value={ospite.nome}
                              disabled
                          />
                    </div>
                    <div class="col-6 col-lg-6 glacialReg">
                        <Label>Cognome</Label>
                        <AvInput 
                              type="text" 
                              name="cognome"
                              value={ospite.cognome}
                              disabled
                        />
                    </div>
                  </div>

                  <div class="row">
                    <div class="col-12 glacialReg">
                          <Label>Email</Label>
                          <AvInput 
                              type="text" 
                              label="Email"
                              name="email"
                              value={ospite.email}
                              disabled
                          />
                    </div>
                    <div class="col-12 ">
                          <Label>Data di nascita</Label>
                          <AvField
                            name="date"
                            value={moment(ospite.dataNascita.split("T")[0], "YYYY-MM-DD").format("DD/MM/YYYY")}
                            type="text"
                            disabled
                          />
                    </div>
                  </div>
                  <hr />
                </div>
              );
    }

    const renderCondizionaleSingole = () => {

      if (this.state.datiCamerePrenotate.numeroSingolePrenotate > 0) {
        return(<div class="row">
                <div class="col-12 my-auto griglia-casa">
                  <AvGroup>
                    <AvField
                      name="singole"
                      label="Numero camere singole"
                      type="number"
                      value={this.state.datiCamerePrenotate.numeroSingolePrenotate}
                      disabled
                    />
                  </AvGroup>
                </div>
              </div>);
      }
    
    }

    const renderCondizionaleDoppie = () => {
      if (this.state.datiCamerePrenotate.numeroDoppiePrenotate > 0) {
          return(
            <div class="row">
            <div class="col-12 my-auto griglia-casa">
              <AvGroup>
                <AvField
                  name="doppie"
                  label="Numero camere doppie"
                  type="number"
                  value={this.state.datiCamerePrenotate.numeroDoppiePrenotate}
                  disabled
                />
              </AvGroup>
            </div>
          </div>
          );
      }
    }

    const renderCondizionaleTriple = () => {
      if (this.state.datiCamerePrenotate.numeroTriplePrenotate > 0) {
      
        return ( <div class="row">
                  <div class="col-12 my-auto griglia-casa">
                    <AvGroup>
                      <AvField
                        name="triple"
                        label="Numero camere triple"
                        type="number"
                        value={this.state.datiCamerePrenotate.numeroTriplePrenotate}
                      />
                    </AvGroup>
                  </div>
                </div>);
      }
    }

    const renderCondizionaleQuadruple = () => {

      if (this.state.datiCamerePrenotate.numeroQuadruplePrenotate > 0) {
        return ( <div class="row">
                    <div class="col-12 my-auto griglia-casa">
                      <AvGroup>
                        <AvField
                          name="quadruple"
                          label="Numero camere quadruple"
                          type="number"
                          value={this.state.datiCamerePrenotate.numeroQuadruplePrenotate}
                        />
                      </AvGroup>
                    </div>
                  </div>);
      }
    }

    const renderCondizionaleExtra = () => {
      
      if (this.state.datiCamerePrenotate.numeroExtraPrenotate > 0) {
        return (<div class="row">
                  <div class="col-12 my-auto griglia-casa">
                    <AvGroup>
                      <AvField
                        name="extra"
                        label={"Numero camera extra"}
                        type="number"
                        value={this.state.datiCamerePrenotate.numeroExtraPrenotate}
                      />
                    </AvGroup>
                  </div>
                </div>);
      }
    }

    const renderCondizionaleBnb = () => {

      if (this.state.datiImmobile.bnb) {
        return  <Card style={{ padding: "5%" }}>
                    <div class="container">
                      {renderCondizionaleSingole()}
                      {renderCondizionaleDoppie()}
                      {renderCondizionaleTriple()}
                      {renderCondizionaleQuadruple()}
                      {renderCondizionaleExtra()}
                    </div>
                </Card>
        }
    }

    return (
      <div class="ez">
        <link
          rel="stylesheet"
          href="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.1.3/css/bootstrap.min.css"
        />

       <div class="container my-auto">
          <Card style={{ padding: "3%" }}>
            <center>
              <h1 class="glacialReg">
                {this.state.datiImmobile.titolo}
              </h1>
            </center>
          </Card>
          <br />
          <br />
          <img
            src={"http://localhost:3000/"+this.state.percorsoFoto}
            width="100%"
            maxWidth="500px"
            height="auto"
            alt="Errore"
          />
        </div>

        <AvForm>
          <div class="container" style={{ paddingTop: "0" }}>
            <div class="row">
              
              <div class="col-sm-12 col-md-12 col-lg-6">
                <Card style={{ padding: "5%" }}>
                  <center><h3 class="glacialReg">Generale</h3></center>
                  <AvForm>
                    
                    <Label>Data di nascita</Label>
                    <AvField
                      name="checkin"
                      value={moment(this.state.datiPrenotazione.checkin, "YYYY-MM-DD").format("DD/MM/YYYY")}
                      type="text"
                      disabled
                    />
                    
                    <Label>Data di nascita</Label>
                        <AvField
                          name="checkin"
                          value={moment(this.state.datiPrenotazione.checkout, "YYYY-MM-DD").format("DD/MM/YYYY")}
                          type="text"
                          disabled
                        />

                    <hr />

                    <AvField
                      name="numeroOspiti"
                      label="Numero ospiti"
                      type="number"
                      value={this.state.listaOspiti.length}
                      disabled
                    />
                  </AvForm>

                  {renderCondizionaleBnb()}
                  <br />
                  <center><h3 class="glacialReg center">TOTALE: € {this.state.datiPrenotazione.saldoTotale}</h3></center>

                </Card>

                
              </div>
              
              <div class="col-sm-12 col-md-12 col-lg-6">
                <Card style={{ padding: "5%" }}>
                  <center><h3 class="glacialReg">Ospiti</h3></center>
                  <AvForm>
                    {renderCondizionaleOspiti()}
                    <center><a href={"http://localhost:3000/"+this.state.percorsoArchivio} style={{color:'red'}}> Clicca qui per scaricare i documenti degli ospiti </a></center>
                  </AvForm>
                </Card>
               </div>7
            </div>
          </div>
        </AvForm>

        <div class="container" style={{ paddingTop: "0" }}>
          <Card style={{ padding: "5%" }}>
            <AvForm>
              <AvGroup>
                <AvInput 
                    type="textarea" 
                    name="motivazioni"  
                    placeholder="Inserisci un commento inserendo le motivazioni di un eventuale rifiuto" 
                    onChange={this.handleChange('motivazioni')} 
                    rows={5}
                />
              </AvGroup>
            </AvForm>
            <center>
              <Button color="secondary" size="lg" class="glacialReg" onClick={this.handleDeclina} disabled={disableDeclina}>DECLINA</Button>&nbsp;&nbsp;
              <Button color="danger" size="lg" class="glacialReg" onClick={this.handleAccetta}>ACCETTA</Button>
            </center>
          
          </Card>
        </div>
      </div>
    );
  }
}

export default PannelloRiepilogoPrenotazione;
