import React, { Component } from "react";
import Footer from "../../components/ComponentiGenerici/Footer";
import Navbar from "../../components/ComponentiGenerici/Navbar";
import PrenotazioneCasa from "./PrenotazioneCasa";
import PrenotazioneBnB from "./PrenotazioneBnB";

export default class PrenotazioneImmobile extends Component {
  state = {
    idImmobile: "",
    isBnb: "",
  };

  componentDidMount() {
    this.state.idImmobile = this.props.match.params.id;
    this.state.isBnb = this.props.match.params.bnb;

    console.log("BBBBBBB");
    console.log(this.state);
  }

  render() {
    if (this.props.match.params.bnb === "0") {
      return (
        <div>
          <Navbar />
          <PrenotazioneCasa idImmobile={this.props.match.params.id} />
          <Footer />
        </div>
      );
    } else {
      return (
        <div>
          <Navbar />
          <PrenotazioneBnB idImmobile={this.props.match.params.id} />
          <Footer />
        </div>
      );
    }
  }
}
