import React, { Component } from "react";
import axios from "axios";

import Card from "./Card";
import "../../componentsCSS/CardsHome.css";

class CardsHome extends Component {
  
  state = {
    annunci: [],
  };

  componentDidMount() {
    axios
      .get(
        "http://localhost:3000/immobiliVetrina",
         {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            "Accept": "application/json",
          },
        }
      )
      .then((res) => {
          console.log(res.data);

          if(res.data.success === true) {
            this.setState({
                annunci: JSON.parse(res.data.listaImmobili),
            })
          }

          
      });
  }

  render() {
    return (
      <div class="container mgT" style={{paddingRight:'0', paddingLeft:'0'}}>
        <div class="row my auto">
          {this.state.annunci.map((annuncio) => (
            <div class="col-12 col-sm-6 col-md-6 col-lg-3 my-auto mgT">
              <Card
                id = {annuncio.ID_Immobile}
                titolo={annuncio.titolo}
                descrizione={annuncio.descrizione}
                prezzo={annuncio.prezzo}
                bnb={annuncio.bnb}
                percorsoImmagine={annuncio.percorsoFoto}
              />
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default CardsHome;
