import React, {Component, useState} from 'react';
import { Card, CardImg, CardText, CardBody, Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

import DateRangeIcon from '@material-ui/icons/DateRange';
import PersonIcon from '@material-ui/icons/Person';
import EuroIcon from '@material-ui/icons/Euro';



class CardStorico extends Component { 

    handleAnnulla = () => {

    }

    render() {

        /* modal di conferma per l'annullamento della prenotazione */
        const AnnullaPrenotazione = (props) => {
            const {
              className
            } = props;
          
            const [modal, setModal] = useState(false);
            const toggle = () => setModal(!modal);
          
            return (
                    <div>
                        
                      <Button color="secondary" onClick={toggle}>Annulla</Button>
    
                      <Modal  isOpen={modal}  toggle={toggle}  className={className}>
                        <ModalHeader toggle={toggle}>Annulla prenotazione</ModalHeader>
                        <ModalBody>
                          Sei sicuro di voler annullare la prenotazione? L'host prevede un rimborso pari al {' '}
                          {this.props.percentualeRimborso}% della cifra già saldata. Pertanto se non hai ancora pagato la prima rata non riceverai alcun rimborso.
                        </ModalBody>
                        <ModalFooter>
                          <Button color="danger" onClick={this.handleAnnulla}>Elimina</Button>
                        </ModalFooter>
                      </Modal>
                    </div>
            );
        }

        /* definisco i collegamenti appropriati in base al tipo di utente e di prenotazione */
        const renderBottoneCondizionale = (props) => {

            if ( this.props.tipoUtente === "host" && this.props.tag === "pendente") {
                    return <Button color="danger" href={"/riepilogo_prenotazione/host&" + this.props.id + "&A"} >Visualizza</Button>
            } 

            else if (this.props.tag === "daEvadere" && this.props.tipoUtente === "utente") {
                    return <div>
                                <Button color="danger" href={"/riepilogo_prenotazione/utente&" + this.props.id + "&" + this.props.tag }>Visualizza</Button><br /><br />
                                <AnnullaPrenotazione />
                            </div>
            } 
            
            else if (this.props.tag === "pendente" && this.props.tipoUtente === "utente") {
                return <Button color="danger" href={"/riepilogo_prenotazione/utente&" + this.props.id + "&" + this.props.tag }>Visualizza</Button>;
            }

            else if (this.props.tag === "archiviata" && this.props.tipoUtente === "utente") {
                return <Button color="danger" href={"/riepilogo_prenotazione/utente&" + this.props.id + "&" + this.props.tag }>Visualizza</Button>;
            }

        }

        const renderEsitoCondizionale = () => {
            if (this.props.tag === "archiviata" && this.props.rifiutata === 1) {
                return <img src="https://image.flaticon.com/icons/svg/845/845648.svg" alt="Errore" height="20px" width="20px" />
            } else if (this.props.tag === "archiviata" && this.props.rifiutata === 0){
                return <img src="https://image.flaticon.com/icons/svg/845/845646.svg" alt="Errore" height="20px" width="20px" />
            } 
        }

        return(
            <Card class="my-auto">
                <CardImg 
                    top width="100%" 
                    src={this.props.foto} 
                    alt="Card image cap"/>
                <CardBody>
                    <center>
                        {renderEsitoCondizionale()}
                        <h4>{this.props.titolo}</h4>
                    </center>
                    <CardText 
                        style={{fontSize:'18px'}} 
                        class="my-auto glacialReg">
                        <hr />    
                        <DateRangeIcon style={{ fontSize: 20 }}/> {this.props.dataCheckIn} <br />
                        <DateRangeIcon style={{ fontSize: 20 }}/> {this.props.dataCheckOut} <br /> 
                        <hr />
                        <PersonIcon style={{ fontSize: 20 }}/> {this.props.nome} {this.props.cognome} <br />
                        <hr />
                        <EuroIcon style={{ fontSize: 20 }}/> {this.props.prezzo}
                        <hr />
                    </CardText>
                    <center>{renderBottoneCondizionale()}</center>
                </CardBody>
            </Card>
        );
    }
}

export default CardStorico;