import React, { Component } from 'react';
import { Button, Input} from 'reactstrap';
import { AvForm, AvField } from 'availity-reactstrap-validation';
import moment from 'moment';

import 'bootstrap/dist/css/bootstrap.min.css';
import '../../componentsCSS/Searchbar.css';

import history from '../../history';


class Searchbar extends Component {
  state ={
    comune: '',
    checkIn: '',
    checkOut: '',
    numOspiti: '', 
    curDate: '',
    minCheckOutDate: '',
  };

  constructor(props) {
    super(props);
    
    this.handleChange = this.handleChange.bind(this);

    /* 
      curDate sarà il lowerbound del checkin
      minCheckOutDate è il lowerbound del checkout 
    */
      this.state.curDate = moment().format('YYYY-MM-DD');
      this.state.minCheckOutDate = moment().format('YYYY-MM-DD');

  }
  

  componentDidMount() {
    console.log("AAAAAAA",localStorage.getItem("ricerca"));
    if (localStorage.getItem("ricerca") !== null) {
        
        this.setState({
          comune: sessionStorage.getItem("comune"),
          checkIn: sessionStorage.getItem("checkin"),
          checkOut: sessionStorage.getItem("checkout"),
          numOspiti: sessionStorage.getItem("nOspiti"),
        })
    }
      
  }

  continue = e =>{
    //fare confronto
    e.preventDefault();
  };

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
    console.log(input, "|", e.target.value)
    // aggiorno il lower bound del checkout
    if (input === "checkIn") {
      this.setState({
        minCheckOutDate:e.target.value
      })
    }
  };

handleSubmit = event => {
    
    if (this.state.numOspiti === "" || this.state.numOspiti === undefined) {
      this.setState({numOspiti:0})
    }

    var ricerca = {
      comune: this.state.comune,
      nOspiti:  this.state.numOspiti,
      checkin: this.state.checkIn,
      checkout: this.state.checkOut,
    }

    sessionStorage.setItem("comune", ricerca.comune);
    sessionStorage.setItem("checkin", ricerca.checkin);
    sessionStorage.setItem("checkout", ricerca.checkout);
    sessionStorage.setItem("nOspiti", ricerca.nOspiti);
    
    history.push("/ricerca/"+ricerca.comune+"&"+ricerca.checkin+"&"+ricerca.checkout+"&"+ricerca.ospiti);
    window.location.reload(false);
  }

  render() {    

    return (
      <div class="bg space my-auto" style={{minHeight:'400px'}}>
        <div class="container my-auto" id="div2">
          <div class="row">

            <div class="col-12 col-sm-12 col-sm-12 col-md-6 col-lg-3 mgT" >
                <Input 
                  type="text"
                  name="comune" 
                  placeholder={this.state.comune !== null ? this.state.comune : "Dove vuoi andare?" }
                  onChange={this.handleChange('comune')}
                /> 
            </div>

            <div class="col-12 col-sm-6 col-sm-6 col-md-6 col-lg-3 mgT">
              <AvForm>
                  <AvField
                    name="checkIn"
                    onChange={this.handleChange('checkIn')}
                    type="date" 
                    min={this.state.curDate}
                    errorMessage="Data non valida" 
                    value={this.state.checkIn}
                  />
              </AvForm>
            </div>
            <div class="col-12 col-sm-6 col-sm-6 col-md-6 col-lg-3 mgT">
              <AvForm>
                  <AvField
                    name="checkOut"
                    onChange={this.handleChange('checkOut')}
                    type="date" 
                    min={this.state.minCheckOutDate} 
                    errorMessage="Data non valida"
                    value={this.state.checkOut}
                  />
              </AvForm>
            </div>
            
            <div class="col-12 col-sm-12 col-sm-6 col-md-6 col-lg-3 mgT">
              <AvForm>
                <AvField 
                  name="numOspiti" 
                  placeholder="Numero ospiti" 
                  type="number" 
                  min="1" 
                  onChange={this.handleChange("numOspiti")} />
              </AvForm>
            </div>
            
            <div class="col-12 col-sm-12 col-sm-12 col-md-12 col-lg-12 my-auto mgT gliacialReg">
              <center><Button type="submit" color="danger" size="md" onClick={this.handleSubmit}>CERCA</Button></center>
            </div>
          </div>
        </div>
      </div>
    );
  }
  
}

export default Searchbar;