import React, { Component } from "react";
import { Card, Button, CardTitle, CardText, UncontrolledCollapse, CardBody} from "reactstrap";
import axios from "axios";
import Navbar from "../../components/ComponentiGenerici/Navbar";
import Footer from "../../components/ComponentiGenerici/Footer";
import history from "../../history";

import "../../componentsCSS/HR.css";
import "../../componentsCSS/AreaRiservata.css";

class AreaRiservataHost extends Component {
  state = {
    host: '',
    email: '',
    restrizioni:'',
  };

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  /* richiedo al database il tipo di utente (host o normale) e l'email */
  componentDidMount() {
    
    //se non vi è una sessione per l'utente, lo reindirizzo al login
    if (localStorage.getItem("sessionValues") === null) {
      history.push("/login");
      window.location.reload(false);
    } else {
      console.log(JSON.parse(localStorage.getItem("sessionValues")));
      // è presente un token, dunque richiedo le informazioni che mi occorrono
      axios
        .post(
          "http://localhost:3000/areaRiservata",
          null,
          {
            params: {
              sessionData: localStorage.getItem("sessionValues"),
            },
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              Accept: "application/json",
            },
          }
        )
        .then((res) => {
          console.log(res);

          if (res.data.success === true) {
            this.setState({
              email: JSON.parse(localStorage.getItem("sessionValues")).email,
              host: JSON.parse(localStorage.getItem("sessionValues")).isHost,
              restrizioni: JSON.parse(localStorage.getItem("sessionValues")).restrizioni,
            });
          }

          console.log(">>>>>>>>>",this.state);
        });
    }
  }

  /* richiesta axios per l'eliminazione dell'account */
  handleElimina = (event) => {
    event.preventDefault();
    axios
      .post(
        "http://localhost:3000/eliminaAccount",
        null,
        {
          params: {
            sessionData: localStorage.getItem("sessionValues"),
            email: this.state.email,
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Accept: "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);

        if (res.data.success === true) {
          alert("Eliminazione avvenuta con successo");
          localStorage.removeItem("sessionValues");
          history.push("/");
          window.location.reload(false);
        }
      });
  };

  render() {
    
    const renderHostComponent = () => {
      if (this.state.host) {
        return (
          <div class="row my-auto">
            <div class="col-12 col-sm-12 col-md-6 col-lg-4 my-auto mgT">
              <Card body class="xd">
                <CardTitle>
                  <b>I MIEI IMMOBILI</b>
                </CardTitle>
                <CardText>
                  <img
                    src="https://cdn0.iconfinder.com/data/icons/christmas-free-icon-pack/128/houses-512.png"
                    height="32px"
                    width="32px"
                    alt="Impossibile caricare la foto"
                  />
                  <br />
                  Visualizza i tuoi fantastici immobili messi a disposizione
                </CardText>
                <Button color="danger" href="/area_riservata/gestione_immobili">
                  Apri
                </Button>
              </Card>
            </div>

            <div class="col-12 col-sm-12 col-md-6 col-lg-4 my-auto mgT">
              <Card body>
                <CardTitle>
                  <b>PRENOTAZIONI RICEVUTE</b>
                </CardTitle>
                <CardText>
                  <img
                    src="https://cdn2.iconfinder.com/data/icons/new-year-resolutions/64/resolutions-16-512.png"
                    height="32px"
                    width="32px"
                    alt="Impossibile caricare la foto"
                  />
                  <br />
                  Visualizza le prenotazioni effettuate{" "}
                </CardText>
                <Button color="danger" href="/area_riservata/storico_entrata" disabled={this.state.restrizioni}>
                  Apri
                </Button>
              </Card>
            </div>

            <div class="col-12 col-sm-12 col-md-6 col-lg-4 my-auto mgT">
              <Card body>
                <CardTitle>
                  <b>GESTIONE CONTABILITÀ</b>
                </CardTitle>
                <CardText>
                  <img
                    src="https://cdn2.iconfinder.com/data/icons/basic-ui-elements-16/117/presentation-512.png"
                    height="32px"
                    width="32px"
                    alt="Impossibile caricare la foto"
                  />
                  <br />
                  Controlla le tue finanze ed effettua i rendiconti trimestrali
                </CardText>
                <Button color="danger" href="/area_riservata/contabilita">
                  Apri
                </Button>
              </Card>
            </div>
          </div>
        );
      }
      return <div></div>;
    };

    const renderDiventaHostComponent = () => {
      if (!this.state.host) {
        return (
          <Button color="danger" size="lg" href="/area_riservata/diventa_host">
            Diventa un host!
          </Button>
        );
      }
    };

    return (
      <div>
        <Navbar />

        <div style={{borderBottom:'solid', borderTop:'solid', borderColor:'#AAAAAA'}}>
          <center>
          <h2 class="glacialReg"  style={{padding:'1%',marginBottom:'0'}} >
            AREA RISERVATA
          </h2>
          </center>
        </div>

        <div style={{minHeight:'83vh'}} class="ez">
          <br />

          <center>
          
            {renderDiventaHostComponent()}
          
          </center>
        
          <div
            align="center"
            class="container"
            style={{ marginBottom: "0%", padding: "3%" }}
          >
            <div class="row my-auto">
              <div class="col-12 col-sm-12 col-md-6 col-lg-4 my-auto">
                <Card body>
                  <CardTitle>
                    <b>MODIFICA PROFILO</b>
                  </CardTitle>
                  <CardText>
                    <img
                      src="https://image.flaticon.com/icons/svg/2919/2919592.svg"
                      height="32px"
                      width="32px"
                      alt="Impossibile caricare la foto"
                    />
                    <br />
                    Modifica il tuo profilo in questa sezione.
                  </CardText>
                  <Button color="danger" href="/area_riservata/modifica_profilo">
                    Apri
                  </Button>
                </Card>
              </div>

              <div class="col-12 col-sm-12 col-md-6 col-lg-4 my-auto mgT">
                <Card body>
                  <CardTitle>
                    <b>MODIFICA PASSWORD</b>
                  </CardTitle>
                  <CardText>
                    <img
                      src="https://cdn4.iconfinder.com/data/icons/twitter-28/512/161_Twitter_Lock_Locked-512.png"
                      height="32px"
                      width="32px"
                      alt="Impossibile caricare la foto"
                    />
                    <br />
                    Modifica la tua password in questa sezione.
                  </CardText>
                  <Button color="danger" href="/area_riservata/modifica_password">
                    Apri
                  </Button>
                </Card>
              </div>

              <div class="col-12 col-sm-12 col-md-6 col-lg-4 my-auto mgT">
                <Card body>
                  <CardTitle>
                    <b>PRENOTAZIONI EFFETTUATE</b>
                  </CardTitle>
                  <CardText>
                    <img
                      src="https://cdn0.iconfinder.com/data/icons/kameleon-free-pack/110/Money-Increase-512.png"
                      height="32px"
                      width="32px"
                      alt="Impossibile caricare la foto"
                    />
                    <br />
                    Visualizza le prenotazioni effettuate
                  </CardText>
                  <Button color="danger" href="/area_riservata/storico_uscita">
                    Apri
                  </Button>
                </Card>
              </div>

              <div class="col-12 col-sm-12 col-md-6 col-lg-4 offset-lg-4 my-auto mgT">
                <Card body>
                  <CardTitle>
                    <b>ELIMINA PROFILO</b>
                  </CardTitle>
                  <CardText>
                    <img
                      src="https://cdn1.iconfinder.com/data/icons/hawcons/32/698761-icon-20-sad-face-eyebrows-512.png"
                      height="32px"
                      width="32px"
                      alt="Impossibile caricare la foto"
                    />
                    <br />
                    Elimina il tuo account
                    <br />
                  </CardText>

                  <Button
                    color="danger"
                    id="toggler"
                  >
                    Elimina profilo
                  </Button>

                  <UncontrolledCollapse toggler="#toggler">
                    <Card>
                      <CardBody>
                        <center>
                          <b> Sei sicuro? </b>
                        </center>
                        <br />
                        <p style={{ textAlign: "justify" }}>
                          Eliminando il profilo perderai tutte le informazioni a
                          te associate. Se hai delle prenotazioni attive ti
                          informeremo via email per eventuali rimborsi.
                        </p>
                        <br />
                        <Button
                          color="danger"
                          id="toggler"
                          style={{ marginBottom: "1rem" }}
                          onClick={this.handleElimina}
                        >
                          Elimina
                        </Button>
                      </CardBody>
                    </Card>
                  </UncontrolledCollapse>
                </Card>
              </div>
            </div>

            {renderHostComponent()}
          </div>
          
        </div> 
        <Footer />
        
      </div>
    );
  }
}

export default AreaRiservataHost;