import React from "react";
import "../../componentsCSS/Footer.css";

const statoUtente = {
  type: "host",
};

function CheckColor() {
  if (
    statoUtente.type === "person" ||
    statoUtente.type === "user" ||
    statoUtente.type === "host"
  )
    return "#dc3545";
  else if (statoUtente.type === "admin") {
    return "#960a00";
  }
}

export default function Footer() {
  return (
    <footer style={{ backgroundColor: CheckColor() }}>
      <p class="glacialReg">
        © 2020 Copyright: DCBs
        <br />
      </p>
    </footer>
  );
}
