import React, { Component } from "react";

import Navbar from "../components/ComponentiGenerici/Navbar";
import FooterFixed from "../components/ComponentiGenerici/FooterFixed";

import "bootstrap/dist/css/bootstrap.min.css";
import "font-awesome/css/font-awesome.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import "./../componentsCSS/Err404.css";

class NotFoundPage extends Component {
  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  render() {
    return (
      <div>
        <Navbar />
        <div class="container" style={{ padding: "10%" }}>
          <div class="row my auto" style={{ padding: "5%" }}>
            
            <div class="col-12 col-sm-12  col-md-12 col-lg-6 my-auto glacialReg" >
              <center>
                <p class="h1">
                  ERROR 404.
                  <hr />
                </p>
                <a href="/" style={{ color: "red", fontSize: "25px" }}>
                  Clicca qui per tornare alla home
                </a>
              </center>
            </div>

            <div class="col-12 col-sm-12 col-md-12 col-lg-6 my-auto">
              <center>
                <img
                  src="https://thumbs.gfycat.com/AccurateUnfinishedBergerpicard-max-1mb.gif"
                  alt="c'&egrave; stato un problema con l'immagine"
                  width="100%"
                ></img>
              </center>
            </div>

            
          </div>
        </div>
        <FooterFixed />
      </div>
    );
  }
}

export default NotFoundPage;
