import React, { Component } from "react";

class Card extends Component {

  render() {
    return (
      <div class="card">
        
        <div class="view">
          <img
            src={"http://localhost:3000/"+this.props.percorsoImmagine}
            class="card-img-top"
            alt="Errore"
            style={{maxWidth:'350px', height:'150px'}}
          />
          <a>
            <div class="mask rgba-white-slight"></div>
          </a>
        </div>

        <div class="card-body text-center" style={{width:'305', height:'250px'}}>
          <h4 class="card-title black-text">{this.props.titolo}</h4>
          <h5 class="card-title black-text">€ {this.props.prezzo}</h5>

          <p class="card-text black-text text-justify">
                    {
                        /* 
                          se la descrizione presenta più di 200 caratteri allora 
                          la limito, altrimenti la visualizzo per intero
                        */
                        
                          this.props.descrizione.length > 100 ? 
                              this.props.descrizione.substring(0,100) + " [...]" :
                              this.props.descrizione 
                    } 
          </p>
          
        </div>
        <a 
          href={"/visualizza_immobile/"+this.props.id} 
          class="btn btn-danger btn-md waves-effect"
          style={{margin:'5%'}}
        >
          Visualizza
        </a>

      </div>
    );
  }
}

export default Card;
