import React from "react";
import "../../componentsCSS/FooterFixed.css";

const statoUtente = {
  type: "user",
};

function CheckColor() {
  if (
    statoUtente.type === "person" ||
    statoUtente.type === "user" ||
    statoUtente.type === "host"
  )
    return "#dc3545";
  else if (statoUtente.type === "admin") {
    return "#960a00";
  }
}

export default function FooterFixed() {
  return (
    <footer style={{ backgroundColor: CheckColor(), position: "fixed" }}>
      <p>
        © 2020 Copyright: DCBs
        <br />
      </p>
    </footer>
  );
}
