import React, {Component} from 'react';
import axios from 'axios';
import { AvForm, AvField, AvGroup, AvInput, AvFeedback, AvRadioGroup, AvRadio } from 'availity-reactstrap-validation';
import { Button, FormGroup, Label,  Card, CardText, Input} from 'reactstrap';
import '../../componentsCSS/GestioneImmobile.css'

import history from '../../history';

class ModificaCasa extends Component {

    state = {
        datiImmobile: '',
        datiSpecifici: '',
        
        titolo: '',     
        descrizione: '',
        regole: '',
        foto: '',
        percorsoFoto: '',
        cap: '',
        comune: '',
        indirizzo: '',
        provincia: '',
        infoLuogo: '',
        numeroBagni: '',
        numeroPostiLetto: '',
        servizi: '',
        prezzoNotte: '',
        costoTasse: '',
        costoPulizia: '',
        esentiTasse: '',
        modPagamento: '',
        modRimborso: '',
        note: '', 
        loaded: '',
    }

    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange = input => e => {
        console.log(e.target.value);
        this.setState({[input]: e.target.value});
    }

    onChangeHandler = event => {
        console.log(event.target.files[0])
        this.setState({
            foto: event.target.files[0],
            loaded: 0,
        });
    
    }

    onValidSubmit = event => {
        event.preventDefault();

        console.log(this.state);
        
        const formData = new FormData();
        formData.append('sessionData', localStorage.getItem("sessionValues"));
        formData.append('idImmobile', this.props.idImmobile);
        formData.append('titolo', this.state.titolo);
        formData.append('descrizione', this.state.descrizione);
        formData.append('regole',this.state.regole);
        formData.append('foto',this.state.foto);
        formData.append('infoLuogo',this.state.infoLuogo);
        formData.append('numeroBagni', this.state.numeroBagni);
        formData.append('numeroPostiLetto', this.state.numeroPostiLetto);
        formData.append('servizi',this.state.servizi);
        formData.append('prezzoNotte',this.state.prezzoNotte);
        formData.append('costoTasse',this.state.costoTasse);
        formData.append('esentiTasse',this.state.esentiTasse);
        formData.append('percentualeRimborso',this.state.modRimborso);
        formData.append('note',this.state.note);

        axios
        .post('http://localhost:3000/modificaCasa', formData, {
            headers: {
                'Content-Type': 'multipart/form-data',
                'Accept': 'application/json'
            }
        })
        .then(res => {
            console.log(res.data);
            if ( res.data.success === true) {
                alert("Modifica eseguita con successo!");
                history.push("/area_riservata/gestione_immobili");
                window.location.reload(false);
            } else {
                alert(res.data.data.message);
            }
        });
    }

    componentDidMount() {
        //se non vi è una sessione per l'utente, lo reindirizzo al login
        if (localStorage.getItem("sessionValues") === null) {
            history.push("/login");
            window.location.reload(false);
        } else {
            // è presente un token, dunque richiedo le informazioni che mi occorrono
            axios
            .get('http://localhost:3000/visualizzaImmobile', {
                params: {
                    idImmobile: this.props.idImmobile
                }
            }, {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Accept': 'application/json'
                }
            })
            .then(res => {
                console.log(res);  

                if (res.data.success === true) {

                    const imm = JSON.parse(res.data.datiImmobile);
                    const spec = JSON.parse(res.data.datiSpecifici);

                    let split = res.data.percorsoFoto.split('/');
                    let campoFoto = '';
                    if (split[0] === "uploads") {
                        campoFoto = split[2];
                    }
                    else {
                        campoFoto = split[1];
                    }
                    
                    this.setState({
                        comune: res.data.comune,
                        titolo: imm.titolo,     
                        descrizione: imm.descrizione,
                        regole: imm.regoleDellaCasa,
                        percorsoFoto: res.data.percorsoFoto,
                        foto: campoFoto,
                        cap: imm.cap,
                        indirizzo: imm.indirizzo,
                        provincia: res.data.provincia,
                        infoLuogo: imm.infoLuogo,
                        numeroBagni: spec.numeroBagni,
                        numeroPostiLetto: spec.postiLetto,
                        servizi: imm.servizi,
                        prezzoNotte: spec.prezzoPerNotte,
                        costoTasse: imm.tasseSoggiorno,
                        esentiTasse: imm.esentiTasseSoggiorno,
                        modPagamento: imm.modalitaPagamento,
                        modRimborso: imm.percentualeRimborso,
                    })
                }
            });                       
        }
        
    }   


    render () {

        return (  
            <div class="ez">

                <div class="header-row">
                <Card body outline color="secondary">
                    <CardText>
                    <justify>
                        <center>
                        <img
                            src={require("../../img/casa.svg")}
                            width="48px"
                            height="48px"
                            alt="Errore"
                        />
                        <p
                            class="h1 glacialReg"
                            style={{color: "#dc3545"}}
                        >
                            MODIFICA CASA
                            <hr />
                        </p>
                        
                        </center>
                            In questa pagina puoi inserire gratuitamente la tua casa, specificando tutte le informazioni necessarie affinché i clienti
                            possano scegliere proprio la tua casa!
                            Mi raccomando, sii accativante e ricorda di tenere con te una foto della tua casa. Ti preghiamo inoltre
                            di essere il più preciso possibile con le informazioni e di non fare brutti scherzi ai tuoi futuri ospiti!
                        <br/>

                    </justify>
                    </CardText>
                </Card>
                </div>

                <div>
                <div class="container" style={{padding:'0px'}}>
                    
                    
                    <AvForm onValidSubmit={this.onValidSubmit} onInvalidSubmit={this.onInvalidSubmit}>

                    <div class="row">
                        
                        <div class="col-12 col-lg-6">
                        <br />
                        {/* CARD INFORMAZIONI GENERALI */}
                        <Card body outline color="secondary">
                            
                            <center>
                            <img src="https://cdn0.iconfinder.com/data/icons/travel-filled-line-4/64/Travel-Filled-17-512.png" width="48px" height="48px" alt="Errore" />
                            <h1 class="glacialReg">Informazioni generali </h1>
                            </center>

                            <br />

                            <CardText >
                            
                            {/* -- titolo -- */}
                            <div class="row">
                                <div class="col-12 my-auto griglia-casa">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Titolo</p>
                                    </Label>
                                </center>
                                </div>

                                <div class="col-12">
                                <AvGroup>
                                    <AvInput
                                    name="titolo"
                                    placeholder="Titolo della casa"
                                    value={this.state.titolo}
                                    onChange={this.handleChange("titolo")}
                                    required
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            </div>

                            <br />

                            {/* -- descrizione -- */}
                            <div class="row">
                                <div class="col-12">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Descrizione</p>
                                    </Label>
                                </center>
                                </div>
                                <div class="col-12">
                                <AvGroup>
                                    <AvInput
                                    type="textarea"
                                    name="descrizione"
                                    placeholder="Inserisci una breve descrizione della casa"
                                    value={this.state.descrizione}
                                    onChange={this.handleChange("descrizione")}
                                    rows={5}
                                    required
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            </div>

                            <br />

                            {/* -- regole della casa -- */}
                            <div class="row" style={{ marginTop: "5px" }}>
                                <div class="col-12 ">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Regole della casa</p>
                                    </Label>
                                </center>
                                </div>
                                <div class="col-12">
                                <AvGroup>
                                    <AvInput
                                    type="textarea"
                                    name="regole"
                                    placeholder="Inserisci le regole della casa"
                                    value={this.state.regole}
                                    rows={4}
                                    onChange={this.handleChange("regole")}
                                    required
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            </div>

                            <br />

                            {/* -- foto della casa -- */}
                            <div class="row" style={{ marginTop: "5px" }}>
                                <div class="col-12">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Foto</p>
                                    </Label>
                                </center>
                                </div>
                                <div class="col-12">
                                <FormGroup>
                                    <Input
                                    type="file"
                                    label="Inserisci una foto dell'immobile"
                                    name="foto"
                                    onChange={this.onChangeHandler}
                                    required
                                    />
                                </FormGroup>
                                </div>
                            </div>
                            
                            <br />

                            </CardText>
                        </Card>
                        
                        </div>
                        
                        <br />

                        <div class="col-12 col-lg-6">
                        <br />
                        {/* CARD POSIZIONE */}
                        <Card body outline color="secondary">
                            
                            <center>
                            <img src="https://cdn1.iconfinder.com/data/icons/flat-and-simple-part-1/128/location-512.png" width="64px" height="64px" alt="Errore" />
                            <h1 class="glacialReg"> Informazioni sul luogo </h1>
                            </center>

                            <br />

                            <CardText>

                            {/* -- indirizzo -- */}
                            <div class="row">
                                
                                <div class="col-12">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Indirizzo</p>
                                    </Label>
                                </center>
                                </div>

                                <div class="col-12 ">
                                <AvGroup>
                                    <AvInput
                                    name="indirizzo"
                                    placeholder="Indirizzo della casa, numero civico"
                                    value={this.state.indirizzo}
                                    required
                                    onChange={this.handleChange("indirizzo")}
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            
                            </div>
                            
                            <br />

                            {/* -- CAP -- */}
                            <div class="row">
                                <div class="col-12">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">CAP</p>
                                    </Label>
                                </center>
                                </div>
                                <div class="col-12">
                                <AvGroup>
                                    <AvInput
                                    name="cap"
                                    id="street"
                                    placeholder=""
                                    value={this.state.cap}
                                    required
                                    onChange={this.handleChange("cap")}
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            </div>
                            
                            <br />

                            {/* -- comune -- */}
                            <div class="row">
                                <div class="col-12">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Comune</p>
                                    </Label>
                                </center>
                                </div>
                                <div class="col-12">
                                <AvGroup>
                                    <AvInput
                                    name="comune"
                                    value={this.state.comune}
                                    required
                                    onChange={this.handleChange("comune")}
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            </div>

                            <br />

                            {/* -- provincia -- */}
                            <div class="row">
                                <div class="col-12">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Provincia</p>
                                    </Label>
                                </center>
                                </div>
                                <div class="col-12">
                                <AvGroup>
                                    <AvInput
                                    name="provincia"
                                    value={this.state.provincia}
                                    required
                                    onChange={this.handleChange("provincia")}
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            </div>
                            
                            <br />

                            {/* -- info luogo -- */}
                            <div class="row">
                                <div class="col-12">
                                <center>
                                    <Label for="example">
                                    <p class="testo-griglia glacialReg">Informazioni sul luogo</p>
                                    </Label>
                                </center>
                                </div>
                                <div class="col-12">
                                <AvGroup>
                                    <AvInput
                                    type="textarea"
                                    name="infoLuogo"
                                    placeholder="Risalta la posizione geografica indicando la presenza di servizi o quant'altro"
                                    value={this.state.infoLuogo}
                                    rows={5}
                                    required
                                    onChange={this.handleChange("infoLuogo")}
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                </AvGroup>
                                </div>
                            </div>

                            </CardText>

                        </Card>
                        
                        </div>
                        
                    </div>
                    
                    <br />


                    <div class="row">
                        
                        <div class="col-12 col-lg-6">
                        <br />
                        {/* CARD INFORMAZIONI SULLA STRUTTURA */}
                        <Card body outline color="secondary">
                            <center>
                            <img
                                src="https://cdn0.iconfinder.com/data/icons/small-n-flat/24/678085-house-512.png"
                                width="48px"
                                height="48px"
                                alt="Errore"
                            />
                            <h1 class="glacialReg"> Informazioni sulla struttura </h1>
                            </center>

                            <br />

                            <CardText>

                            <div class="row">

                                {/* -- posti letto -- */}
                                <div class="col-12 glacialReg">
                                    <center>
                                        <AvGroup>
                                        <Label for="example">
                                            <p class="testo-griglia glacialReg">Posti letto</p>
                                        </Label>

                                        <AvField
                                            name="numeroPostiLetto"
                                            type="number"
                                            min="1"
                                            value="1"
                                            onChange={this.handleChange("numeroPostiLetto")}
                                        />
                                        </AvGroup>
                                    </center>
                                </div>

                                <div class="col-12">
                                    <center>
                                    <AvGroup>
                                        <Label for="example">
                                        <br />
                                        <p class="testo-griglia glacialReg">Numero bagni</p>
                                        </Label>
                                        <AvField
                                        name="numeroBagni"
                                        type="number"
                                        min="1"
                                        value="1"
                                        onChange={this.handleChange("numeroBagni")}
                                        />
                                    </AvGroup>
                                    </center>
                                </div>
                            </div>

                            <div class="row " style={{ marginTop: "1%" }}>
                                <div class="col-12">
                                <center>
                                    <AvGroup>
                                    <Label>
                                        <br />
                                        <p class="testo-griglia glacialReg">Servizi</p>
                                    </Label>
                                    <AvInput
                                        type="textarea"
                                        name="servizi"
                                        placeholder="Descrivi i servizi offerti"
                                        value={this.state.servizi}
                                        rows={5}
                                        required
                                        onChange={this.handleChange("servizi")}
                                    />
                                    <AvFeedback>Campo obbligatorio!</AvFeedback>
                                    </AvGroup>
                                </center>
                                </div>
                            </div>
                            </CardText>
                        </Card>
                        
                        </div>

                        <br />

                        {/* INFORMAZIONI SUL PAGAMENTO */}
                        <div class="col-12 col-lg-6">
                            <br />
                            <Card body outline color="secondary">
                            <center>
                                <img
                                    src="https://cdn0.iconfinder.com/data/icons/kameleon-free-pack/110/Money-Increase-512.png"
                                    width="80px"
                                    height="80px"
                                    alt="Errore"
                                />
                                <h1 class="glacialReg"> Informazioni sul pagamento </h1>

                            </center>

                            <br />
                            
                            <CardText>
                                
                                {/* -- prezzo per notte -- */}
                                <div class="col-12">
                                    <center>
                                    <Label>
                                        <br />
                                        <p class="testo-griglia glacialReg">Prezzo per notte</p>
                                    </Label>
                                    <AvGroup>
                                        <AvField
                                        name="prezzoNotte"
                                        value={this.state.prezzoNotte}
                                        type="number"
                                        min="1"
                                        value="1"
                                        onChange={this.handleChange("prezzoNotte")}
                                        />
                                    </AvGroup>
                                    </center>
                                </div>

                                {/* -- tasse di soggiorno -- */}
                                <div class="col-12">
                                    <center>
                                    <Label>
                                        <br />
                                        <p class="testo-griglia glacialReg">Costo tasse di soggiorno</p>
                                    </Label>
                                    <AvGroup>
                                        <AvField
                                        name="costoTasse"
                                        type="number"
                                        value={this.state.costoTasse}
                                        min="1"
                                        value="1"
                                        onChange={this.handleChange("costoTasse")}
                                        />
                                    </AvGroup>
                                    </center>
                                </div>
                                
                                
                                {/* -- esenti tasse di soggiorno -- */}
                                <div class="col-12">
                                    <center>
                                    <Label>
                                        <br />
                                        <p class="testo-griglia glacialReg">Esenti tasse di soggiorno</p>
                                    </Label>
                                    <AvGroup>
                                        <AvInput
                                        type="textarea"
                                        name="esentiTasse"
                                        value={this.state.esentiTasse}
                                        placeholder="Illustra gli esenti dalle tasse di soggiorno"
                                        required
                                        onChange={this.handleChange("esentiTasse")}
                                        />
                                        <AvFeedback>Campo obbligatorio!</AvFeedback>
                                    </AvGroup>
                                    </center>
                                </div>

                                {/* -- esenti tasse di soggiorno -- */}
                                <div class="col-12 ">
                                    <center>
                                    <Label>
                                        <br />
                                        <p class="testo-griglia glacialReg">Modalità di pagamento</p>
                                    </Label>
                                    </center>
                                    <div class="col-12 col-sm-12 col-md-12 col-lg-9">
                                    <AvRadioGroup  name="modPagamento" required errorMessage="Campo obbligatorio!" value={this.state.modPagamento}>
                                        <AvRadio value="online" label="100% subito" onChange={this.handleChange("modPagamento")}  />
                                        <AvRadio value="dilazionato" label="50% subito e 50% tre giorni prima del check-in" onChange={this.handleChange("modPagamento")} />
                                        <AvRadio value="metà in loco" label="50% subito e 50% in loco" onChange={this.handleChange("modPagamento")} />
                                    </AvRadioGroup>
                                    </div>
                                </div>

                                {/* -- moalità di rimborso -- */}
                                <div class="col-12">
                                    <center>
                                    <Label>
                                        <br />
                                        <p class="testo-griglia glacialReg">Percentuale rimborso</p>
                                    </Label>
                                    </center>
                                    <AvGroup>
                                    <AvField
                                        name="modRimborso"
                                        type="number"
                                        value={this.state.modRimborso}
                                        min="0"
                                        value="0"
                                        onChange={this.handleChange("modRimborso")}
                                    />
                                    </AvGroup>
                                </div>
                            
                            </CardText>
                        
                            </Card>
                        
                        </div>
                        
                    </div>

                <br />

                {/* -- BOTTONE CONFERMA -- */}
                <center>
                    <br />
                    <Button  color="danger" size="lg" type="submit" >Modifica casa</Button>
                    <br />
                    <br />
                </center>

                </AvForm>
            </div>
            </div>
        </div>
  
        );
    }
}
export default ModificaCasa;