import React, { Component } from "react";
import axios from 'axios';
import history from '../../history';

import Footer from "../../components/ComponentiGenerici/Footer";
import Navbar from "../../components/ComponentiGenerici/Navbar";

import { Jumbotron, Button } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";

export default class Registrazione extends Component {
  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  constructor() {
    super();

    var today = new Date(),
      date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();

    this.state = {
      date: date,
    };
  }

  state = {
    nome: '',
    cognome: '',
    email: '',
    dataNascita: '',
    password: '',
  }

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  onValidSubmit = (event) => {
    event.preventDefault();
    axios
      .post(
        "http://localhost:3000/registrazioneUtente",
        null,
        {
          params: {
            nome: this.state.nome,
            cognome: this.state.cognome,
            email: this.state.email,
            passwordUtente: this.state.password,
            dataNascita: this.state.dataNascita,
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Accept: "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);

        if (res.data.success === true) {
          alert("Registrazione effettuata con successo!");
          history.push("/login");
          window.location.reload(false);
        } else {
          alert(res.data.message);
        }
      });
  };

  render() {

    // definisco il lower bound per la data di nascita
    const now = new Date().toJSON().split('T')[0];
    let eighteenYearsAgo = new Date();    
        eighteenYearsAgo.setFullYear(eighteenYearsAgo.getFullYear() - 18);
        eighteenYearsAgo = eighteenYearsAgo.toJSON().split('T')[0];

    return (
      <div>
        <Navbar />
        
        <AvForm style={{minHeight:'83vh'}} onValidSubmit={this.onValidSubmit}>

          <div class="row h-100 justify-content-md-center" style={{ margin: "5%" }}>
            <div class="col-sm-12 col-md-8 col-lg-6 my-auto">
              <Jumbotron>

                  <center>
                    <p class="glacialReg" style={{fontSize:'40px', color:'rgb(220, 53, 69)'}}>DC<img src="https://image.flaticon.com/icons/svg/3069/3069205.svg" alt="Errore" width="80px" height="80px" />Booking <br /></p>
                  </center>

                  <br />
                  <hr style={{backgroundColor:'rgb(220, 53, 69)'}}/>

                  {/* Riga nome e cognome */}
                  <div class="row">

                    <div class="col-12 col-md-6">
                        <AvField
                          name="nome"
                          type="text"
                          label="Nome"
                          onChange={this.handleChange("nome")}
                          validate={{
                            required: {
                              value: true,
                              errorMessage: "Il campo è richiesto",
                            },
                          }}
                        />
                    </div>

                    <div class="col-12 col-md-6">
                      <AvField
                        name="cognome"
                        type="text"
                        label="Cognome"
                        onChange={this.handleChange("cognome")}
                        required
                        validate={{
                          required: {
                            value: true,
                            errorMessage: "Il campo è richiesto.",
                          },
                        }}
                      />
                    </div>
                  </div>
                  
                  <br />
                  <hr style={{backgroundColor:'rgb(220, 53, 69)'}}/>
                  
                  {/* Riga data di nascita */}
                  <div class="row">

                    <div class="col-12">
                      <AvField
                        name="dataNascita"
                        label="Data di nascita"
                        type="date"
                        max={eighteenYearsAgo}
                        onChange={this.handleChange("dataNascita")}
                        errorMessage="Data non valida"
                        validate={{
                          required: {
                            value: true,
                            errorMessage: "Il campo è richiesto.",
                          },
                        }}
                      />
                    </div>

                  </div>

                  <br />
                  <hr style={{backgroundColor:'rgb(220, 53, 69)'}}/>

                  {/*Riga email */}
                  <div class="row">
                    <div class="col-12">
                      <AvField
                        name="email"
                        label="Email"
                        type="email"
                        onChange={this.handleChange("email")}
                        errorMessage="Campo non valido."
                        required
                      />
                    </div>
                  </div>
                  
                  <br />
                  <hr style={{backgroundColor:'rgb(220, 53, 69)'}}/>

                  {/* Riga password */}
                  <div class="row">

                    <div class="col-12 col-md-6">
                      <AvField
                        name="password"
                        label="Password"
                        type="password"
                        validate={{
                          required: { value: true },
                          minLength: { value: 6 },
                        }}
                        errorMessage="La password deve contenere almeno 8 caratteri"
                        onChange={this.handleChange("password")}
                      />
                    </div>

                    <div class="col-12 col-md-6">
                      <AvField
                        name="confirmationPsw"
                        label="Conferma Password"
                        type="password"
                        validate={{
                          match: {
                            value: "password",
                            errorMessage: "Le password devono combaciare",
                          },
                        }}
                        errorMessage="Il campo è richiesto."
                        required
                      />
                    </div>

                  </div>

                  <br />
                  <hr style={{backgroundColor:'rgb(220, 53, 69)'}}/>
                  
                  <center>
                    
                    <Button color="danger" type="submit"> 
                      Registrati
                    </Button>
                  
                  </center>

              </Jumbotron>
            </div>
          </div>

        </AvForm>
        
        <Footer />
      </div>
    );
  }
}
