/*main.jsx*/
import React, { Component } from "react";
import axios from "axios";

import {Card} from 'reactstrap';

import Footer from "../../components/ComponentiGenerici/Footer";
import Navbar from "../../components/ComponentiGenerici/Navbar";
import CardStorico from "../../components/Card/CardStorico";

import moment from 'moment';
import history from '../../history';

export default class storicoEntrata extends Component {
  state = {
    prenotazioni: [],
    prenotazioniPendenti: []
  };

  componentDidMount() {
    //se non vi è una sessione per l'utente, lo reindirizzo al login
    if (localStorage.getItem("sessionValues") === null) {
        history.push("/login");
        window.location.reload(false);
    } else {
        axios
        .get(
          "http://localhost:3000/prenotazioniRicevute",
          {
            params: {
              sessionData: localStorage.getItem("sessionValues"), 
            },
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              Accept: "application/json",
            },
          }
        )
        .then((res) => {
          console.log(res);

          if (res.data.success === true) {
            this.setState({
              prenotazioniPendenti: JSON.parse(res.data.prenotazioni)
            });
          }
        });
      }
    
  }

  render() {

    /* 
      controllo la dimensione dei due array per stampare l'output appropriato
    */
    const renderPrenotazioni = () => {
            
      if( this.state.prenotazioni.length !== 0 ) {
        return  this.state.prenotazioni.map(prenotazione => 
                  <div class="col-12 col-sm-12 col-md-6 col-lg-4 mgT">
                      <CardStorico
                          foto = {/*"http://localhost:3000/"+*/prenotazione.percorsoFoto}
                          titolo = {prenotazione.titolo}
                          dataCheckIn = {moment(prenotazione.checkin.split("T")[0], "YYYY-MM-DD").format("DD/MM/YYYY")}
                          dataCheckOut = {moment(prenotazione.checkout.split("T")[0], "YYYY-MM-DD").format("DD/MM/YYYY")}
                          nome = {prenotazione.nome}
                          cognome = {prenotazione.cognome}
                          prezzo = {prenotazione.saldoTotale}
                          id = {prenotazione.ID_Prenotazione}
                          tag = "archiviata"
                          tipoUtente = "host"
                          rifiutata = {prenotazione.rifiutata}
                      />
                  </div>
                )
      } 

      return  <h4>
                Sembra che tu non abbia delle prenotazioni in attesa di approvazione!
              </h4>;
    }

    const renderPrenotazioniPendenti = () => {
            
      if( this.state.prenotazioniPendenti.length !== 0 ) {
        
        return  this.state.prenotazioniPendenti.map(prenotazione => 

                  <div class="col-12 col-sm-12 col-md-6 col-lg-4 mgT">
                      <CardStorico
                          foto = {"http://localhost:3000/" + prenotazione.percorsoFoto}
                          titolo = {prenotazione.titolo}
                          dataCheckIn = {moment(prenotazione.checkin.split("T")[0], "YYYY-MM-DD").format("DD/MM/YYYY")}
                          dataCheckOut = {moment(prenotazione.checkout.split("T")[0], "YYYY-MM-DD").format("DD/MM/YYYY")}
                          nome = {prenotazione.nome}
                          cognome = {prenotazione.cognome}
                          prezzo = {prenotazione.saldoTotale}
                          id = {prenotazione.ID_Prenotazione}
                          tipoUtente = "host"
                          tag = "pendente"
                      />
                  </div>
                )
      } 

      return  <h4>
                Sembra che tu non abbia richiesto alcuna prenotazione!
              </h4>;
    }
    

    return (
      <div class="ez">
        <Navbar />

        <div style={{borderBottom:'solid', borderTop:'solid', backgroundColor: "white", borderColor:'#AAAAAA'}}>
          <center>
          <h2 class="glacialReg"  style={{padding:'1%',marginBottom:'0'}} >
            PRENOTAZIONI RICEVUTE
          </h2>
          </center>
        </div>

        <br /><br />

        <div class="container glacialReg" style={{ paddingTop: "0", marginTop: "0"}}>
          <Card style={{padding:'3%'}}>
            <center>
              <img src="https://image.flaticon.com/icons/svg/3132/3132720.svg" alt="Errore" height="64px" width="64px" />
              <h3 class="glacialReg" style={{color:'rgb(220, 53, 69)'}}>Prenotazioni pendenti</h3>
            </center>
            <div class="row">
                  {renderPrenotazioniPendenti()}
            </div>
          </Card>
        </div>

        <div class="container glacialReg" style={{ paddingTop: "0", marginTop: "0"}}>
          <Card style={{padding:'3%'}}>
            <center>
              <img src="https://image.flaticon.com/icons/svg/3068/3068566.svg" alt="Errore" height="64px" width="64px" />
              <h3 class="glacialReg"style={{color:'rgb(220, 53, 69)'}}>Prenotazioni archiviate</h3>
            </center>
            <div class="row">
                  {renderPrenotazioni()}
            </div>
          </Card>
        </div>

        <Footer />
      </div>
    );
  }
}
