import React, {Component, useState} from 'react';
import axios from 'axios';

import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';

import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Tooltip} from 'reactstrap';




export default class CardMieiImmabili extends Component {

  handleElimina = event => {
      event.preventDefault();

      console.log(this.props.idImmobile);
      axios
        .post('http://localhost:3000/cancellaImmobile', null, {
            params: {
                sessionData: localStorage.getItem("sessionValues"),
                idImmobile: this.props.idImmobile
            }
        }, {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'Accept': 'application/json'
            }
        })
        .then(res => {
            console.log(res);    
            
            if(res.data.success === true) {
                alert("Eliminazione avvenuta con successo!")
                window.location.reload(false);
            } else {
                alert(res.message);    
            }
        });
  };

  handleOscura = event => {
    event.preventDefault();

      console.log(this.props.idImmobile);
      axios
        .post('http://localhost:3000/oscuraImmobile', null, {
            params: {
                idImmobile: this.props.idImmobile
            }
        }, {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'Accept': 'application/json'
            }
        })
        .then(res => {
            console.log(res);    
            
            if(res.data.success === true) {
                alert("Oscuramento avvenuto con successo!")
                window.location.reload(false);
            } else {
              alert(res.message);
            }
        });
  };

  handleDeoscura = event => {
    event.preventDefault();

      console.log(this.props.idImmobile);
      axios
        .post('http://localhost:3000/rimuoviOscuramentoImmobile', null, {
            params: {
                idImmobile: this.props.idImmobile
            }
        }, {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'Accept': 'application/json'
            }
        })
        .then(res => {
            console.log(res);    
            
            if(res.data.success === true) {
                alert("Deoscuramento avvenuto con successo!");
                window.location.reload(false);
            } else {
              alert(res.message);
            }
        });
  }

  

    render () {

      // modal di conferma per eliminare l'immobile
      const Elimina = (props) => {
        const {
          className
        } = props;

        const [tooltipOpen, setTooltipOpen] = useState(false);
        const toggleT = () => setTooltipOpen(!tooltipOpen);
      
        const [modal, setModal] = useState(false);
        const toggle = () => setModal(!modal);
      
        return (
                <div>
                  <Button  color="danger"  onClick={toggle}  style={{backgroundColor:'transparent', border:'none'}} id={"Tooltip-1"} >
                    <DeleteIcon style={{ color: 'red' }} />
                  </Button>

                  <Tooltip
                    placement={"bottom"}
                    isOpen={tooltipOpen}
                    target={"Tooltip-" + 1}
                    toggle={toggleT}
                  >
                    Elimina immobile
                  </Tooltip>


                  <Modal  isOpen={modal}  toggle={toggle}  className={className}>
                    <ModalHeader toggle={toggle}>Elimina immobile</ModalHeader>
                    <ModalBody>
                      Sei sicuro di voler eliminare l'immobile? Sarai costretto a rimborsare eventuali utenti che hanno già prenotato un soggiorno.
                    </ModalBody>
                    <ModalFooter>
                      <Button color="danger" onClick={this.handleElimina}>Elimina</Button>
                    </ModalFooter>
                  </Modal>
                </div>
        );
      }

      // modal di conferma per oscurare l'immobile
      const Oscura = (props) => {
        const {
          className
        } = props;

        const [tooltipOpen, setTooltipOpen] = useState(false);
        const toggleT = () => setTooltipOpen(!tooltipOpen);
      
        const [modal, setModal] = useState(false);
        const toggle = () => setModal(!modal);
      
        return (
          <div>
            <Button  color="danger" onClick={toggle} style={{backgroundColor:'transparent', border:'none'}} id={"Tooltip-2"}>
              <VisibilityOffIcon style={{ color: 'red' }} />
            </Button>

            <Tooltip
              placement={"bottom"}
              isOpen={tooltipOpen}
              target={"Tooltip-2"}
              toggle={toggleT}
            >
              Oscura immobile
            </Tooltip>

            <Modal  isOpen={modal}  toggle={toggle}  className={className}>
              <ModalHeader toggle={toggle}>Oscura immobile</ModalHeader>
              <ModalBody>
                Sei sicuro di voler oscurare l'immobile? Così facendo il tuo immobile non sarà più presente nelle ricerche, ma dovrai garantire le prenotazioni già ricevute.
              </ModalBody>
              <ModalFooter>
                <Button color="danger" onClick={this.handleOscura}>Oscura</Button>
              </ModalFooter>
            </Modal>
          </div>
        );
      }

      // modal di conferma per deoscuare l'immobile
      const Deoscura = (props) => {
        const {
          className
        } = props;
      
        const [modal, setModal] = useState(false);
        const toggle = () => setModal(!modal);

        const [tooltipOpen, setTooltipOpen] = useState(false);
        const toggleT = () => setTooltipOpen(!tooltipOpen);
      
        return (
          <div>
            <Button color="danger" onClick={toggle}  style={{backgroundColor:'transparent', border:'none'}} id={"Tooltip-3"}>
              <VisibilityIcon style={{ color: 'red' }} />
            </Button>

            <Tooltip
              placement={"bottom"}
              isOpen={tooltipOpen}
              target={"Tooltip-3"}
              toggle={toggleT}
            >
              Deoscura immobile
            </Tooltip>

            

            <Modal isOpen={modal} toggle={toggle} className={className} >
                <ModalHeader toggle={toggle}>Deoscura immobile</ModalHeader>
                <ModalBody>
                  Sei sicuro di voler deoscurare l'immobile? Così facendo il tuo immobile tornerà ad essere presente nelle ricerche.
                </ModalBody>
              <ModalFooter>
                <Button color="danger" onClick={this.handleDeoscura}>
                  Deoscura
                </Button>
              </ModalFooter>
            </Modal>
          </div>
        );
      }

      // tooltip per il bottone "Modifica"
      const TooltipModifica = props => {
        const [tooltipOpen, setTooltipOpen] = useState(false);
      
        const toggle = () => setTooltipOpen(!tooltipOpen);
      
        return (
          <span>
            <Button  
              href={"/area_riservata/gestione_immobili/modifica_immobile/" + this.props.idImmobile + "&" + this.props.isBnb } 
              id={"Tooltip-4"}
              style={{backgroundColor:'transparent', border:'none'}}
            >
              <EditIcon style={{ color: 'red' }}/>
            </Button>
            
            <Tooltip
              placement={"bottom"}
              isOpen={tooltipOpen}
              target={"Tooltip-4"}
              toggle={toggle}
              style={{border:'none'}}
            >
              Modifica immobile
            </Tooltip>
          </span>
        );
      };

      // gestisco un render condizionale a seconda delle restrizioni e 
      // dello stato corrente degli immobili
      const renderOtherOptions = () => {

        /* 
            Se l'utente ha delle restrizioni non può visualizzare le opzioni atte a gestire i propri immobili 
            Altrimenti, se non possiede delle restrizioni può oscurare o deoscurare l'immobile a seconda dello 
            corrente
        */
        if (this.props.restrizioni === 1) {
          return;
        } 
        
        else if (this.props.visibile === 1 ) {
          return (<div class="row">
                    <div class="col-4 col-md-4 col-lg-12">
                        <TooltipModifica />
                    </div>
                    <div class="col-4 col-md-4 col-lg-12">
                        <Elimina />
                    </div>
                    <div class="col-4 col-md-4 col-lg-12">
                        <Oscura />
                    </div>
                  </div>);
        }
        
        else if (this.props.visibile === 0) {
          return (<div class="row">
                    <div class="col-4 col-md-4 col-lg-12">
                        <TooltipModifica />
                    </div>
                    <div class="col-4 col-md-4 col-lg-12">
                        <Elimina />
                    </div>
                    <div class="col-4 col-md-4 col-lg-12">
                        <Deoscura />
                    </div>
                  </div>);
        } 
      }
    
    return (
      <div class="container">

        

        <div class="row" style={{backgroundColor:'#e8ecef', marginBottom:'2%'}}>

              <div class="col-12 sol-sm-12 col-md-12 col-lg-5" style={{marginTop:'2%'}}>
                    <center>
                      <img src={this.props.foto} style={{width:'30vh'}}alt="Errore" />
                    </center>
              </div> 

              <div class="col-12 sol-sm-12 col-md-12 col-lg-5" style={{marginTop:'2%'}}>
                  <center>
                      <h4>{this.props.titolo}</h4>
                      <p class="text-justify"> 

                        
                        {
                        
                        /* 
                          se la descrizione presenta più di 200 caratteri allora 
                          la limito, altrimenti la visualizzo per intero
                        */
                        
                        
                          this.props.descrizione.length > 220 ? 
                              this.props.descrizione.substring(0,220) + " [...]" :
                              this.props.descrizione 
                        } 
                      
                      </p>
                  </center>
              </div> 

              <div class="col-12 sol-sm-12 col-md-12 col-lg-2" style={{marginTop:'2%'}}>
                  <center>
                    
                    {renderOtherOptions()}
                    
                    <br/><br />
                    
                    <Button color="danger" href={"/visualizza_immobile/"+this.props.idImmobile}>Vedi di più</Button>
                  </center>
              </div>
              
        </div>
      </div>  
        

    );
  }
}