import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch} from "react-router-dom";

// -- Admin
import LoginAdmin from "./pages/Admin/LoginAdmin";
import PannelloControlloAdmin from './pages/Admin/PannelloControlloAdmin';

// -- Autenticazione
import Login from './pages/Autenticazione/Login';
import RecuperoPassword from './pages/Autenticazione/RecuperoPassword';
import Registrazione from './pages/Autenticazione/Registrazione';
import AreaRiservata from './pages/FunzionalitaAccount/AreaRiservata';

// -- Funzionalità Account
import ModificaProfilo from './pages/FunzionalitaAccount/ModificaProfilo';
import ModificaPassword from './pages/FunzionalitaAccount/ModificaPassword';
import DiventaHost from './pages/FunzionalitaAccount/DiventaHost';
import StoricoEntrata from './pages/FunzionalitaAccount/StoricoEntrata';
import StoricoUscita from './pages/FunzionalitaAccount/StoricoUscita';

// -- Gestione Immobili
import GestioneImmobili from './pages/GestioneImmobili/GestioneImmobili';
import InserisciCasa from './pages//GestioneImmobili/InserisciCasa';
import InserisciBnB from './pages//GestioneImmobili/InserisciBnB';
import InserisciBnB2 from './pages//GestioneImmobili/InserisciBnB2';
import ModificaImmobile from './pages//GestioneImmobili/ModificaImmobile';

// -- Gestione Contabilità
import GestioneContabilita from "./pages/GestioneContabilita/GestioneContabilita";

// -- Gestione Ricerca
import Ricerca from './pages/GestioneRicerca/Ricerca';
import VisualizzaImm from './pages/GestioneRicerca/VisualizzaImm';


// -- Gestione Prenotazione
import PrenotazioneImmobile from './pages/GestionePrenotazione/PrenotazioneImmobile';
import RiepilogoPrenotazione from './pages/GestionePrenotazione/RiepilogoPrenotazione';

// -- Gestione Pagamento
import Pagamento from './pages/GestionePagamento/Pagamento';

// -- 
import Main from "./pages";
import PageNotFound from "./pages/404";
import history from './history'

class App extends Component {

  render() {
    return (
      <Router history={history}>
        <Switch>
          
          {/* Route generiche */}
          <Route exact path="/" component={Main} />
          <Route exact path="/area_riservata" component={AreaRiservata}/>    
          <Route exact path="/login" component={Login} />
          <Route exact path="/registrazione" component={Registrazione} />
          <Route exact path="/recuperoPassword" component={RecuperoPassword} />
          <Route path="/ricerca/:comune&:checkin&:checkout&:nOspiti" component={Ricerca} />
          <Route path="/ricerca" component={Ricerca} />
          <Route exact path="/visualizza_immobile/:id" component={VisualizzaImm} />
          
          {/* Route area riservata */}
          <Route exact path="/area_riservata/storico_entrata" component={StoricoEntrata} />
          <Route exact path="/area_riservata/storico_uscita" component={StoricoUscita} />
          <Route exact path="/area_riservata/modifica_profilo" component={ModificaProfilo} />
          <Route exact path="/area_riservata/modifica_password" component={ModificaPassword} />
          <Route exact path="/area_riservata/gestione_immobili/inserisci_casa" component={InserisciCasa} />
          <Route exact path="/area_riservata/gestione_immobili/inserisci_bnb" component={InserisciBnB} />
          <Route exact path="/area_riservata/gestione_immobili/modifica_immobile/:id&:tipoImmobile" component={ModificaImmobile} />
          <Route exact path="/area_riservata/gestione_immobili" component={GestioneImmobili} />
          <Route exact path="/area_riservata/contabilita" component={GestioneContabilita} />
          <Route exact path="/area_riservata/diventa_host" component={DiventaHost} />

          {/* Route prenotazione */}
          <Route exact path="/visualizza_immobile/prenota/:bnb&:id" component={PrenotazioneImmobile} />
          <Route exact path="/pagamento/:tipo&:id" component={Pagamento} />
          <Route exact path="/pagamento/:tipo&:comune&:id" component={Pagamento} />
          <Route exact path="/riepilogo_prenotazione/:tipoUtente&:id&:tag" component={RiepilogoPrenotazione} />

          {/* Route admin */}
          <Route exact path="/admin" component={LoginAdmin} />
          <Route exact path="/admin/pannello_di_controllo" component={PannelloControlloAdmin} />
          
          {/* Route di default */}
          <Route component={PageNotFound} />

        </Switch>
      </Router> 
    );
  }
}

export default App;