import React, { Component } from "react";
import axios from "axios";
import history from "../../history";

import "bootstrap/dist/css/bootstrap.min.css";


export default class Navbar extends Component {
  
  state = {
    logged: '',
  }


  componentDidMount() {
    axios
      .get(
        "http://localhost:3000/isLoggedIn",
        {
          params: {
            sessionData: localStorage.getItem("sessionValues"),
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            "Accept": "application/json",
          },
        }
      )
      .then((res) => {
        
          if (res.data.success) {
            this.setState({logged: true})
          } 
      });
  }

  handleLogout = () => {
      axios
      .post(
        "http://localhost:3000/logoutUtente",
        null,
        {
          params: {
            sessionData: localStorage.getItem("sessionValues"),
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Accept: "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);
        console.log(res.data);

        if (res.data.success === true) {
          alert("Logout eseguito con successo!");
          console.log(res.data);
          localStorage.removeItem("sessionValues");
          history.push("/");
          window.location.reload(false);
        } else {
          alert(res.data.message);
        }
      });
  }

  handleLogin = () => {
    history.push("/login");
    window.location.reload(false);
  }


  render() {

    const CheckStatus = () => {
      if (!this.state.logged) {
        return <div></div>;
      } else {
        return (
          <div
            class="collapse navbar-collapse flex-grow-0"
            id="navbarSupportedContent"
          >
            <ul class="navbar-nav text-right">
              <li class="nav-item active">
                <a class="nav-link glacialReg" href="/area_riservata">
                  Area Riservata
                </a>
              </li>
            </ul>
          </div>
        );
      }
    }

    const CollapseBtn = () => {
      if (this.state.logged) {
        return (
          <button
            class="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
          >
            <span class="navbar-toggler-icon"></span>
          </button>
        );
      } else {
        return <div></div>;
      }
    }

    const BottoneLog = () => {
      if (!this.state.logged) {
        return (
          <button class="btn btn-light ml-auto mr-1 glacialReg" onClick={this.handleLogin}>
            Accedi
          </button>
        );
      } else {
        return (
          <button class="btn btn-light ml-auto mr-1 glacialReg" onClick={this.handleLogout}>
            Esci
          </button>
        );
      }
    }

    return (
      <nav
        class="navbar navbar-expand-sm navbar-dark justify-content-end"
        style={{ backgroundColor: "#dc3545" }}
      >
        <a class="navbar-brand glacialReg" href="/" style={{fontSize: "30px" }} >
          DCBooking
        </a>

        {BottoneLog()}
        {CollapseBtn()}
        {CheckStatus()}

      </nav>
    );
  }
}
