import React, { Component } from "react";

import Footer from '../../components/ComponentiGenerici/Footer';
import Navbar from "../../components/ComponentiGenerici/Navbar";
import PannelloRiepilogoPrenotazione from "./PannelloRiepilogoPrenotazione";
import PannelloRiepilogoPrenotazioneUtente from "./PannelloRiepilogoPrenotazioneUtente";

export default class Login extends Component {
  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  render() {
    if (this.props.match.params.tipoUtente === "host") {
      return (
        <div>
          <Navbar />
          <PannelloRiepilogoPrenotazione idPrenotazione = {this.props.match.params.id} />
          <Footer />
        </div>
      );
    } else {
      
      return (
        <div>
          <Navbar />
          <PannelloRiepilogoPrenotazioneUtente idPrenotazione={this.props.match.params.id} tag = {this.props.match.params.tag}/>
          <Footer />
        </div>
      );
    }
  }
}
