import React, { Component } from "react";
import axios from "axios";
import { Button, Card, CardText } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";

import history from "../../history";

export default class LoginAdmin extends Component {
  state = {
    email: "",
    psw: "",
  };

  handleSubmit = (e) => {
    //fare confronto
    e.preventDefault();
    
    axios
    .post('http://localhost:3000/loginAdmin', null, {
        params: {
            email: this.state.email,
            password: this.state.psw
        }
    }, {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Accept': 'application/json'
        }
    })
    .then(res => {
        console.log(res);
        if ( res.data.success === true) {

            alert("Login eseguito con successo!");
            localStorage.setItem('id',res.data.idAdmin);

            history.push("/admin/pannello_di_controllo");
            window.location.reload(false);
            
        } else {
            alert(res.data.message);
        }
    });

  };

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
    console.log(this.state);
  };

  render() {
    
    return (
            <div class="container d-flex h-100" style={{ margin: "auto" }}>

              <Card body outline color="danger" style={{ borderRadius: "0rem !important" }} >

                <CardText>
                  
                  <AvForm class="text-center"  onSubmit={this.handleSubmit}>
                    
                    <center>
                      <img src="https://image.flaticon.com/icons/svg/2014/2014167.svg" height="64px" width="64px" alt="Errore"  />
                      <h3 class="glacialReg"> ADMIN </h3>
                    </center>
                    
                    <br />

                    <div class="row">
                      <div class="col-12">
                        <AvField
                          autofocus
                          name="email"
                          type="email"
                          label="Inserisci la tua mail.."
                          placeholder="@"
                          onChange={this.handleChange("email")}
                          required
                          errorMessage="Non sembra tu abbia inserito una mail"
                          
                        />
                      </div>
                    </div>

                    <br />
                    
                    <div class="row">
                      <div class="col-12">
                        <AvField
                          name="psw"
                          type="password"
                          label="Inserisci la tua password.."
                          placeholder="*"
                          onChange={this.handleChange("psw")}
                          required
                          errorMessage="Non sembra tu abbia inserito una password"
                        />
                      </div>
                    </div>

                    <br />
                    <br />

                    <center><Button color="danger">Login</Button></center>
                    
                    </AvForm>
                </CardText>
              </Card>
            </div>
    );
  }
}
