import React from "react";
import axios from "axios";
import { AvForm } from "availity-reactstrap-validation";
import { Button, ListGroup, ListGroupItem, Input, Card } from "reactstrap";

import Navbar from '../../components/ComponentiGenerici/Navbar';
import Footer from '../../components/ComponentiGenerici/Footer';



class VisualizzaImmobile extends React.Component {
  
  state = {
    user: '',
    titolo:'',
    descrizione:'',
    dataArr: '',
    dataFin: '',
    servizi:'',
    regole:'',
    pagamenti:'', 
    info:'',
    prezzo:'',
    activeIndex:'',
    foto: '',
    comune: '',

    isBNB: true,
    numeroSingole: 0,
    numeroDoppie: 0,
    numeroTriple: 0,
    numeroQuadruple: 0  ,
    numeroExtra: 0,
    personeExtra: 0,

    singoleSelezionate: '',
    doppieSelezionate: '',
    tripleSelezionate: '',
    quadrupleSelezioante: '',
    extraSelezionate: '',

    immobile: [],
    datiSpecificiCasa: [],
    datiSpecificiBNB: [],
    modPagamento: "",

    disabledButton: false
    
  }

  componentDidMount() {
    console.log(">>><<<");
    // prelevo le informazioni della casa
    axios
      .get(
        "http://localhost:3000/visualizzaImmobile",
        {
          params: {
            idImmobile: window.location.pathname.split("/")[2]
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            "Accept": "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);
        console.log("AAAAAAAAAAAAAAAAAAAAAAAAA");

        if (res.data.success === true) {
          
          this.setState({
            immobile: JSON.parse(res.data.datiImmobile),
            comune: res.data.comune,
            foto: res.data.percorsoFoto
          });

          if (this.state.immobile.bnb) {
            this.setState({
              datiSpecificiBNB: JSON.parse(res.data.datiSpecifici),
              datiSpecificiCasa: undefined
            })
          } else {
            this.setState({
              datiSpecificiCasa: JSON.parse(res.data.datiSpecifici),
              datiSpecificiBNB: undefined
            })
          }

          /* definisco un commento testuale per le modalità di pagamento e di rimborso */
          if (this.state.immobile.modalitaPagamento === "online") {
            this.setState({
              modPagamento: "PAGA 100% ONLINE" 
            })
          } else if (this.state.immobile.modalitaPagamento === "metà in loco") {
            this.setState({
              modPagamento: "PAGA 50% ORA E 50% IN LOCO" 
            })
          } else if (this.state.immobile.modalitaPagamento === "dilazionato") {
            this.setState({
              modPagamento: "PAGA 50% ORA E 50% UNA SETTIMANA PRIMA (SEMPRE ONLINE)" 
            })
          }

          if (this.state.immobile.percentualeRimborso === 0) {
            this.setState({percentualeRimborso:"Il proprietario non ha previsto alcuna percentuale di rimborso per il seguente annuncio" });
          } else {
            this.setState({percentualeRimborso: ("Il proprietario ha previsto un rimborso pari al " 
                                                  + this.state.immobile.percentualeRimborso + 
                                                  "% per un'eventuale annullamento della prenotazione. Ad esempio su una prenotazione di €100, verrai rimborsato di €" + this.state.immobile.percentualeRimborso+"." )});
          }
          
          
        }


      });
  }

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  

  render() {
    
    const renderCondizionaleSingole = () => {

          if (this.state.immobile.bnb && this.state.datiSpecificiBNB.numeroSingole > 0) {
              return(
                <ListGroupItem tag="a" action>
                  <img src="https://image.flaticon.com/icons/svg/3100/3100538.svg" height="32px" width="32px" alt="Errore" />
                  <h5 class="glacialReg">Numero stanze singole: {this.state.datiSpecificiBNB.numeroSingole}</h5>
                  <h5 class="glacialReg">Prezzo stanze singole: {this.state.datiSpecificiBNB.prezzoSingole}€</h5>
                </ListGroupItem>
              )
          }
    }

    const renderCondizionaleDoppie = () => {

      if (this.state.immobile.bnb && this.state.datiSpecificiBNB.numeroDoppie > 0) {
          return(
            <ListGroupItem tag="a" action>
              <img src="https://image.flaticon.com/icons/svg/3100/3100644.svg" height="32px" width="32px" alt="Errore" />
              <h5 class="glacialReg">Numero stanze doppie: {this.state.datiSpecificiBNB.numeroDoppie}</h5>
              <h5 class="glacialReg">Prezzo stanze doppie: {this.state.datiSpecificiBNB.prezzoDoppie}€</h5>
            </ListGroupItem>
          )
      }
    }

    const renderCondizionaleTriple = () => {

      if (this.state.immobile.bnb && this.state.datiSpecificiBNB.numeroTriple > 0) {
          return(
            <ListGroupItem tag="a" action>
              <img src="https://image.flaticon.com/icons/svg/3100/3100569.svg" height="32px" width="32px" alt="Errore" />
              <h5 class="glacialReg">Numero stanze triple: {this.state.datiSpecificiBNB.numeroTriple}</h5>
              <h5 class="glacialReg">Prezzo stanze triple: {this.state.datiSpecificiBNB.prezzoTriple}€</h5>
            </ListGroupItem>
          )
      }
    }

    const renderCondizionaleQuadruple = () => {

      if (this.state.immobile.bnb && this.state.datiSpecificiBNB.numeroQuadruple > 0) {
          return(
            <ListGroupItem tag="a" action>
              <img src="https://image.flaticon.com/icons/svg/3100/3100661.svg" height="32px" width="32px" alt="Errore" />
              <h5 class="glacialReg">Numero stanze quadruple: {this.state.datiSpecificiBNB.numeroQuadruple}</h5>
              <h5 class="glacialReg">Prezzo stanze quadruple: {this.state.datiSpecificiBNB.prezzoQuadruple}€</h5>
            </ListGroupItem>
          )
      }
    }

    const renderCondizionaleExtra = () => {

      if (this.state.immobile.bnb && this.state.datiSpecificiBNB.numeroQuadruple > 0) {
          return(
            <ListGroupItem tag="a" action>
              <img src="https://image.flaticon.com/icons/svg/3100/3100762.svg" height="32px" width="32px" alt="Errore" />
              <h5 class="glacialReg">Numero stanze per {this.state.datiSpecificiBNB.personeCameraExtra}: {this.state.datiSpecificiBNB.numeroQuadruple}</h5>
              <h5 class="glacialReg">Prezzo stanze per {this.state.datiSpecificiBNB.personeCameraExtra}: {this.state.datiSpecificiBNB.prezzoExtra}€</h5>
            </ListGroupItem>
          )
      }
    }

    const renderBoxStruttura = () => {
        if (!this.state.immobile.bnb) {
          return (
              <center>
                <ListGroup class="center">
                    <ListGroupItem active tag="button" action style={{backgroundColor:'rgb(220, 53, 69)', border:'none'}}>
                      <h3 class="glacialReg">La struttura</h3>
                    </ListGroupItem>
                    <ListGroupItem tag="a" action>
                      <img src="https://image.flaticon.com/icons/svg/2906/2906488.svg" height="32px" width="32px" alt="Errore" />
                      <h5 class="glacialReg">Posti letto: {this.state.datiSpecificiCasa.postiLetto}</h5>
                    </ListGroupItem>
                    <ListGroupItem tag="a" action>
                      <img src="https://image.flaticon.com/icons/svg/3075/3075376.svg" height="32px" width="32px" alt="Errore" />
                      <h5 class="glacialReg">Bagni: {this.state.datiSpecificiCasa.numeroBagni}</h5>
                    </ListGroupItem>
                    <ListGroupItem tag="a" action>
                      <img src="https://image.flaticon.com/icons/svg/3004/3004164.svg" height="32px" width="32px" alt="Errore" />
                      <h5 class="glacialReg">Prezzo per notte: {this.state.datiSpecificiCasa.prezzoPerNotte}€</h5>
                    </ListGroupItem>
                </ListGroup>
              </center>)
        } else {
          return (
            <center>
              <ListGroup class="center">
                  <ListGroupItem active tag="button" action color="danger">
                    <h3 class="glacialReg">La struttura</h3>
                  </ListGroupItem>
                  
                  {renderCondizionaleSingole()}
                  {renderCondizionaleDoppie()}
                  {renderCondizionaleTriple()} 
                  {renderCondizionaleQuadruple()}
                  {renderCondizionaleExtra()}

                  
              </ListGroup>
            </center>)
        }
    }

    return (

      <div class="ez">
        <Navbar />
        <link rel="stylesheet"  href="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.1.3/css/bootstrap.min.css" />

        <div class="container my-auto">
          <Card style={{ padding: "3%" }}>
            <center>
              <h1 class="glacialReg">
                {this.state.immobile.titolo}
              </h1>
            </center>
            <p>{this.state.immobile.descrizione}</p>
            <br />
          </Card>

          <br />
          <br />
          
          <center>
            <img
              src={"http://localhost:3000/"+this.state.foto}
              height="auto"
              alt="Errore"
              style={{maxWidth:'50vw', height:'auto'}}
            />
          </center>
        </div>

        <AvForm>
          <div class="container" style={{paddingTop:'0'}}>

            {/* -- STRUTTURA -- */}
            <div class="container" style={{paddingTop:'0', marginTop:'0'}}>
                <div class="row">
                  <div class="col-12">
                    <AvForm>
                      
                      {renderBoxStruttura()}
                      
                    </AvForm>
                  </div>

                  
                </div>
            </div>
            
            
            <div class="row">
              
              <div class="col-sm-12 col-md-12 col-lg-6" >
                  {/* -- SERVIZI -- */}
                  <ListGroup style={{padding:'5%'}}>
                    <ListGroupItem active tag="button" action style={{backgroundColor:'rgb(220, 53, 69)', border:'none'}}>
                      <h3 class="glacialReg">Servizi</h3>
                    </ListGroupItem>
                    <Card style={{padding:'5%', minHeight:'200px'}}>
                      <p class="glacialReg">{this.state.immobile.servizi} </p>
                    </Card>
                  </ListGroup>

                  <br/>
                  
                  {/* -- REGOLE DELLA CASA -- */}
                  <ListGroup style={{padding:'5%'}}>
                      <ListGroupItem active tag="button" action style={{backgroundColor:'rgb(220, 53, 69)', border:'none'}}>
                          <h3 class="glacialReg">Regole della casa</h3>
                      </ListGroupItem>
                      <Card style={{padding:'5%', minHeight:'200px'}}>
                        <p class="glacialReg">{this.state.immobile.regoleDellaCasa}</p>
                      </Card>
                  </ListGroup>
              </div>

              
              <div class="col-sm-12 col-md-12 col-lg-6" >
                {/* -- MOD. PAGAMENTO E RIMBORSO -- */}
                <ListGroup className="" style={{padding:'5%'}}>
                  <ListGroupItem active tag="button" action style={{backgroundColor:'rgb(220, 53, 69)', border:'none'}}>
                    <h3 class="glacialReg">Modalità di pagamento e rimborso</h3>
                  </ListGroupItem>
                  <Card style={{padding:'5%', minHeight:'200px'}} >
                    <p class="glacialReg">
                      {this.state.modPagamento}
                      <hr />
                      {this.state.percentualeRimborso}
                    </p>
                  </Card>
                </ListGroup>

                <br/>
                
                {/* -- INFORMAZIONI SUL LUOGO -- */}
                <ListGroup style={{padding:'5%'}} >
                  <ListGroupItem active tag="button" action style={{backgroundColor:'rgb(220, 53, 69)', border:'none'}}>
                    <h3 class="glacialReg">Informazioni sul luogo</h3>
                  </ListGroupItem>
                  <Card style={{padding:'5%', minHeight:'200px'}}>
                    <p class="glacialReg">{this.state.immobile.infoLuogo} </p>
                  </Card>
                </ListGroup>
              </div>
            </div>
          </div>
        </AvForm>

        <center>
            <Button 
                color="danger" 
                size="lg" 
                style={{marginBottom:'5%'}} 
                href={"prenota/"+this.state.immobile.bnb+"&"+this.state.immobile.ID_Immobile}
                disabled={this.state.disabledButton}
            >
                Clicca qui per prenotare
            </Button>
            
           
        </center>

        <Footer />
      </div>
    );
  }
}

export default VisualizzaImmobile;
