import React, { Component } from "react";
import axios from "axios";

import { Button } from "reactstrap";
import { Jumbotron, Container } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";

import Navbar from "../../components/ComponentiGenerici/Navbar";
import FooterFixed from "../../components/ComponentiGenerici/FooterFixed";
import history from "../../history";

class ModificaProfilo extends Component {

  constructor(props) {
    super(props);
    this.handlePassword = this.handlePassword.bind(this); 

    this.state = {
      confirmationPsw: "",
      password: "",
      vecchiaPassword: "",
    };
  }

  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  componentDidMount() {
    //se non vi è una sessione per l'utente, lo reindirizzo al login
    if (localStorage.getItem("sessionValues") === null) {
      history.push("/login");
      window.location.reload(false);
    }
  }

  handlePassword = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  handleInputChange = (event) => {
    this.setState({
      [event.target.name]: event.target.value,
    });
  };


  /* richiesta axios per la modifica della password */
  continue = (e) => {
    e.preventDefault();

    axios
      .post(
        "http://localhost:3000/modificaPassword",
        null,
        {
          params: {
            sessionData: localStorage.getItem("sessionValues"),
            passwordVecchia: this.state.vecchiaPassword,
            passwordNuova: this.state.password,
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Accept: "application/json",
          },
        }
      )
      .then((res) => {

        if (res.data.success === true) {
          
          alert("Password modificata con successo!");
          history.push("/area_riservata");
          window.location.reload(false);

        } else {
          alert(res.data.message);
        }
      });
  };


  render() {

    const enableButton = this.state.vecchiaPassword.length > 0 && 
                         this.state.password.length > 0 &&
                         this.state.confirmationPsw.length > 0;

    return (
      <div>
        <Navbar />

        <div style={{borderBottom:'solid', borderTop:'solid', backgroundColor: "white", borderColor:'#AAAAAA'}}>
          <center>
          <h2 class="glacialReg"  style={{padding:'1%',marginBottom:'0'}} >
            MODIFICA PASSWORD
          </h2>
          </center>
        </div>

        <div
          class="row h-100 justify-content-md-center"
          style={{ margin: "5%" }}
        >
          <div class="col-sm-12 col-md-8 col-lg-6 my-auto">
            <Jumbotron>
              <Container fluid>
                <center>
                  <img
                    src="https://cdn4.iconfinder.com/data/icons/twitter-28/512/161_Twitter_Lock_Locked-512.png"
                    height="64px"
                    width="64px"
                    alt="Impossibile caricare la foto"
                  />
                </center>
                <AvForm>
                  <AvField
                    name="vecchiaPassword"
                    label="Vecchia password"
                    type="password"
                    onChange={this.handlePassword("vecchiaPassword")}
                    errorMessage="Campo obbligatorio"
                    required
                  />
                  <AvField
                    name="password"
                    label="Nuova password"
                    type="password"
                    errorMessage="Campo obbligatorio"
                    onChange={this.handlePassword("password")}
                    required
                  />
                  <AvField
                    name="confirmationPsw"
                    label="Conferma nuova password"
                    errorMessage="Campo obbligatorio"
                    type="password"
                    validate={{
                      match: {
                        value: "password",
                        errorMessage: "Le password devono combaciare",
                      },
                    }}
                    onChange={this.handlePassword("confirmationPsw")}
                    required
                  />
                  <hr className="my-2" />

                  <center>
                    <Button color="danger" onClick={this.continue} disabled={!enableButton}>
                      Conferma
                    </Button>
                  </center>
                </AvForm>
              </Container>
            </Jumbotron>
          </div>
        </div>
        <FooterFixed />
      </div>
    );
  }
}

export default ModificaProfilo;
