import React, { Component } from "react";
import axios from "axios";
import {
  Row,
  Button,
  FormGroup,
  FormText,
  Input,
  Jumbotron,
  Container,
} from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";

import history from "../../history";
import Navbar from "../../components/ComponentiGenerici/Navbar";
import Footer from "../../components/ComponentiGenerici/Footer";

class ModificaProfilo extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this); /*ipnut file*/

    this.state = {
      codiceFiscale: "",
      tipoDocumento: "",
      indirizzo: "",
      documentoHost: "",
    };
  }

  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  onChangeHandler = (event) => {
    console.log(event.target.files[0]);
    this.setState({
      documentoHost: event.target.files[0],
      loaded: 0,
    });

    console.log(this.state.documentoHost);
  };

  continue = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("sessionData", localStorage.getItem("sessionValues"));
    formData.append("docHost", this.state.documentoHost);
    formData.append("codiceFiscale", this.state.codiceFiscale);
    formData.append("tipoDocumento", this.state.tipoDocumento);

    console.log(formData);

    axios
      .post("http://localhost:3000/diventaHost", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          Accept: "application/json",
        },
      })
      .then((res) => {
        console.log(res);
        console.log(res.data);

        if (res.data.success === true) {
          alert("Richiesta inviata! La richiesta prevede un controllo manuale da parte di un nostro operatore, riceverai notizie non appena sarà valutata.");
          history.push("/area_riservata");
          window.location.reload(false);
        } else {
          alert(res.data.message);
        }
      });
  };

  componentDidMount() {}

  render() {

    const enableButton =  this.state.codiceFiscale.length === 16 && 
                          this.state.tipoDocumento.length > 0 && 
                          this.state.documentoHost !==  "";

    return (
      <div>
        <Navbar />

        <div
          class="row h-100 justify-content-md-center"
          style={{ margin: "5%" }}
        >
          <div class="col-sm-12 col-md-8 col-lg-6 my-auto">
            <Jumbotron>
              <Container fluid>
                <center>
                  <img
                    src="https://image.flaticon.com/icons/svg/2513/2513018.svg"
                    height="64px"
                    width="64px"
                    alt="Impossibile caricare la foto"
                  />
                  <br />
                </center>

                <Row form>
                  <p
                    class="text-justify glacialReg"
                    style={{ marginTop: "5%", marginBottom: "5%" }}
                  >
                    Diventare un host nel nostro sito è semplice e gratuito!
                    Inoltre avrai la possibilità non solo di poter pubblicare le tue case e i tuoi BnB, 
                    ma anche di tener traccia delle tue entrate e di tutto il lato finanziario della gestione
                    del tuo account tramite la nostra semplice interfaccia per gli host!
                  </p>
                </Row>

                <AvForm>

                  <AvField
                    name="codiceFiscale"
                    label="Codice fiscale"
                    type="text"
                    placeholder="Inserisci il tuo codice fiscale"
                    onChange={this.handleChange("codiceFiscale")}
                    validate={{
                      required: {
                        value: true,
                        errorMessage: "Non hai inserito il codice fiscale",
                      },
                      pattern: {
                        value: /[A-Z]{6}[0-9]{2}[A-Z]{1}[0-9]{2}[A-Z0-9]{4}[A-Z]{1}/, 
                        errorMessage:'Non sembra tu abbia inserito un Codice Fiscale'
                      }
                    }}
                  />

                  <br />

                  <AvField
                    name="tipoDocumento"
                    label="Tipo documento"
                    type="text"
                    placeholder="Inserisci il tipo del documento"
                    onChange={this.handleChange("tipoDocumento")}
                    validate={{
                      required: {
                        value: true,
                        errorMessage: "Non hai inserito il tipo del documento",
                      },
                    }}
                  />

                  <br />

                  <FormGroup row>
                    <Input
                      type="file"
                      name="documentoHost"
                      id="exampleFile"
                      onChange={this.onChangeHandler}
                    />
                    <FormText color="muted">
                      Allega la scansione del tuo documento
                    </FormText>
                  </FormGroup>

                  <center>
                    <Button color="danger" onClick={this.continue} disabled={!enableButton}>
                      Conferma
                    </Button>
                  </center>

                </AvForm>
              </Container>
            </Jumbotron>
          </div>
        </div>

        <Footer />
      </div>
    );
  }
}

export default ModificaProfilo;
