import React, { Component } from "react";
import axios from "axios";

import Footer from "../../components/ComponentiGenerici/Footer";
import Navbar from "../../components/ComponentiGenerici/Navbar";


import { Card, CardText, Button} from "reactstrap";
import LineChart from "../../components/Contabilita/LineChart";
import "../../componentsCSS/HR.css";
import "../../componentsCSS/AreaRiservata.css";

import history from "../../history";

class GestioneContabilita extends Component {

  state = {
    rendicontiDaEvadere: [],
    entrateMensili: [],
    entrateTotali: 1000,
    uscite: 500,
    ospiti: 43,
    data: [0, 0, 0, 0, 0, 0, 30, 0, 0, 0, 0, 0],
  };

  componentDidMount() {

    console.log("ARRA DATA: ", this.state.data);

    //se non vi è una sessione per l'utente, lo reindirizzo al login
    if (localStorage.getItem("sessionValues") === null) {
      history.push("/login");
      window.location.reload(false);
    } else {

        /*
          richiedo i guadagni mensili e alcuni dati per l'overview generale 
        */

        axios
          .get(
            "http://localhost:3000/datiContabilita",
            {
              params: {
                sessionData: localStorage.getItem("sessionValues"),
                anno: new Date().getFullYear() 
              },
            },
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                "Accept": "application/json",
              },
            }
          )
          .then((res) => {
            console.log("GENERALE CONTABILITA >>>>> ",res);

            if (res.data.success === true) {
              this.setState({
                entrateTotali: res.data.entrateTotali,
                uscite: res.data.usciteTotali,
                ospiti: res.data.totaleOspiti,
                entrateMensili: res.data.guadagniMensili
              });

              console.log("ANTO: ",this.state.entrateMensili);

            }
          });

        /*
            per semplicità faccio una seconda richiesta per ottenere eventuali rendiconti 
            pendenti 
        */
        axios
          .get(
            "http://localhost:3000/comuniDaRendicontare",
            {
              params: {
                sessionData: localStorage.getItem("sessionValues")
              },
            },
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                "Accept": "application/json",
              },
            }
          )
          .then((res) => {
            console.log(res);

            if (res.data.success === true) {
              this.setState({
                rendicontiDaEvadere: JSON.parse(res.data.listaComuni),
              });
            }
          })
    }
  }

  render() {

    const renderCondizionaleRendiconti = () => {

      if (this.state.rendicontiDaEvadere.length > 0) {
        return  this.state.rendicontiDaEvadere.map(rendiconto => 
                  <div class="col-4 col-sm-3 col-md-3 col-lg-3">
                    <center>
                        <a href="#">
                            <img src="https://image.flaticon.com/icons/svg/975/975727.svg" class="glacialReg" title={rendiconto.nome} width="40px" height="40px"/>
                            <p style={{color:'black'}} class="glacialBold">{rendiconto.nome}</p>   
                            <Button size="sm" color="outline-danger" href={"/pagamento/rendiconto&"+ rendiconto.id_comune}>PAGA</Button>                                 
                        </a>
                    </center>
                  </div>
                );
      } else {
          return <center>
                    <h3 class="glacialReg">
                      Attualmente sei in regola con i rendiconti passati e non vi sono delle tasse di soggiorno da versare.  
                    </h3>
                 </center>;
      }

    }

    return (
      <div>
        <Navbar />

          <div style={{borderBottom:'solid', borderTop:'solid', backgroundColor: "white", borderColor:'#AAAAAA'}}>
            <center>
            <h2 class="glacialReg"  style={{padding:'1%',marginBottom:'0'}} >
              CONTABILITÀ
            </h2>
            </center>
          </div>
        
          <div class="ez">

            {/* grafico entrate mensili */}
            <div class="container my-auto">
              <Card style={{ padding: "2%" }}>
                <LineChart datiMensili = {this.state.entrateMensili}/>
              </Card>
            </div>

            {/* overview generale */}
            <div class="container" style={{ padding: "2%"}}>
              <div class="row">

                {/* card guadagni complessivi */}
                <div class="col-12 col-md-4 mgT" >
                  <center>
                    <Card
                      body
                      outline
                      color="secondary"
                      style={{ width: "150px", height: "150px", borderRadius: "20rem", }}
                    >
                      <center>
                        <img
                          src="https://image.flaticon.com/icons/svg/2867/2867785.svg"
                          height="75px"
                          width="75px"
                          alt="Errore"
                        />
                      </center>
                      <h4 class="glacialReg black"> € {this.state.entrateTotali}  </h4>
                    </Card>
                  </center>
                </div>

                {/* card tasse pagate */}
                <div class="col-12 col-md-4 mgT">
                  <center>
                    <Card
                      body
                      outline
                      color="secondary"
                      style={{
                        width: "150px",
                        height: "150px",
                        borderRadius: "20rem",
                      }}
                    >
                    <center>
                      <img
                        src="https://image.flaticon.com/icons/svg/2473/2473058.svg"
                        height="75px"
                        width="75px"
                        alt="Errore"
                      />
                    </center>
                    <h4 class="glacialReg"> - € {this.state.uscite} </h4>
                    </Card>
                  </center>
                </div>
                
                {/* card totale ospiti */}
                <div class="col-12 col-md-4 mgT my-auto">
                  <center>
                    <Card
                      body
                      outline
                      color="secondary"
                      style={{
                        width: "150px",
                        height: "150px",
                        borderRadius: "20rem"
                      }}
                    >
                    <center>
                      <img
                        src="https://image.flaticon.com/icons/png/512/1006/1006106.png"
                        height="64px"
                        width="64px"
                        alt="Errore"
                      />
                    </center>  
                        <h4 class="glacialReg">  {this.state.ospiti} ospiti </h4>

                    </Card>
                  </center>
                </div>

                
              </div>
            </div>

            <div class="container my-auto">
              <Card style={{ padding: "2%" }}>
                <center>
                  <img
                    src="https://image.flaticon.com/icons/svg/747/747156.svg"
                    width="48px"
                    height="48px"
                    alt="Errore"
                  />
                  <h1 class="glacialReg"> RENDICONTI PERIODICI</h1>
                  <br />
                </center>

                <CardText>
                  
                  <div class="row">
                  
                      
                      {renderCondizionaleRendiconti()}

                    
                  </div>

                </CardText>
              </Card>
            
            </div>

            
          </div>
          <Footer />
      </div>
    );
  }
}

export default GestioneContabilita;
