import React, { Component } from 'react';
import axios from 'axios';
import { Jumbotron, Container, Button} from 'reactstrap';
import {AvForm,AvField} from 'availity-reactstrap-validation';

import history from '../../history';
import Footer from '../../components/ComponentiGenerici/Footer';
import Navbar from '../../components/ComponentiGenerici/Navbar';

export default class Login extends Component {

    state={
        email: '',
        psw: ''
    };

    handleChange = input => e => {
        this.setState({[input]: e.target.value});
    }

    onValidSubmit = event => {
        event.preventDefault();
        axios
            .post('http://localhost:3000/loginUtente', null, {
                params: {
                    email: this.state.email,
                    passwordUtente: this.state.psw
                }
            }, {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Accept': 'application/json'
                }
            })
            .then(res => {
                console.log(res.data);
                if ( res.data.success === true) {
                    alert("Login eseguito con successo!");
                    localStorage.setItem('sessionValues', JSON.stringify(res.data.session));
                    history.push("/");
                    window.location.reload(false);
                } else {
                    alert(res.data.message);
                }
            });
    }

    render() {

        return (

              <div class="ez">
                
                <Navbar />

                <div class="row h-100 justify-content-md-center" style={{margin:'5%', minHeight:'83vh'}} >
                    <div class="col-sm-12 col-md-8 col-lg-6 my-auto">
                        <Jumbotron>
                            <Container fluid>
                                
                                <center><img src="https://image.flaticon.com/icons/svg/3004/3004069.svg" height="64px" width="64px" alt="Errore"/></center>

                                <AvForm onValidSubmit={this.onValidSubmit}>
                                    
                                    <AvField
                                        autofocus
                                        name="email"
                                        type="email"
                                        label="Inserisci la tua mail.."
                                        placeholder="@"
                                        onChange={this.handleChange('email')}
                                        errorMessage="Non sembra tu abbia inserito una mail"
                                        required
                                    />
                                    
                                    <br />
                                    
                                    <AvField
                                        name="psw"
                                        type="password"
                                        label="Inserisci la tua password.."
                                        placeholder="*"
                                        onChange={this.handleChange('psw')}
                                        errorMessage = "Campo obbligatorio"
                                        required
                                    />

                                    <br />  
                                    <center>
                                        <Button type="submit" color="danger">Login</Button>
                                    </center>
                                    <br />  
                                    
                                </AvForm>
                            </Container>
                            
                            <center>Non hai un account? <a href="/registrazione" style={{color:'red'}}>Registrati</a></center>
                            <center>Hai smarrito la password? <a href="/recuperoPassword" style={{color:'red'}}>Recupera Password</a></center>
                            <center>Sei un admin? <a href="/admin" style={{color:'red'}}>Clicca qui</a></center>
                        
                        </Jumbotron >
                    </div>
                </div>   

                <Footer />
              </div>
        )
    }
}
