import React, { Component } from "react";
import axios from "axios";

import { Jumbotron, Container, Button } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";

import Navbar from "../../components/ComponentiGenerici/Navbar";
import FooterFixed from "../../components/ComponentiGenerici/FooterFixed";

import history from "../../history";

export default class recuperoPassword extends Component {
  componentWillMount() {
    document.body.style.backgroundImage =
      "url(https://cdn.discordapp.com/attachments/689756758365044758/718552810144268358/photoshoppata.jpg)";
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = null;
  }

  state = {
    email: "",
  };

  continue = (e) => {
    //fare confronto
    e.preventDefault();
  };

  handleChange = (input) => (e) => {
    this.setState({ [input]: e.target.value });
  };

  onValidSubmit = (event) => {
    event.preventDefault();
    
    axios
      .post(
        "http://localhost:3000/recuperoPassword",
        null,
        {
          params: {
            email: this.state.email,
          },
        },
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Accept: "application/json",
          },
        }
      )
      .then((res) => {
        console.log(res);
        console.log(res.data);

        if (res.data.success === true) {
          alert("Ti abbiamo inviato una email con la nuova password!");
          history.push("/login");
          window.location.reload(false);
        } else {
          alert(res.data.message);
        }
      });
  };
  


  render() {

    /* se l'utente non inserisce una mail allora il bottone non sarà disponibile */
    const enableButton = this.state.email.length > 0; 

    return (
      <div>
        <Navbar />

        <div
          class="row h-100 justify-content-md-center"
          style={{ margin: "5%" }}
        >
          <div class="col-sm-12 col-md-8 col-lg-6 my-auto">
            <Jumbotron>
              <Container fluid>
                <center>
                  <img
                    src="https://image.flaticon.com/icons/svg/2534/2534673.svg"
                    height="64px"
                    width="64px"
                    alt="Impossibile caricare la foto"
                  />
                </center>

                <br />
                <AvForm onValidSubmit={this.onValidSubmit} >
                  <AvField
                    autofocus
                    name="email"
                    type="email"
                    label="Inserisci la tua mail.."
                    placeholder="@"
                    onChange={this.handleChange("email")}
                    errorMessage="Non sembra tu abbia inserito una mail"
                    validate={{
                      required: {
                        value: true,
                        errorMessage: "Il campo è richiesto",
                      },
                    }}
                    required
                  />

                  <br />
                  <center>
                    <Button color="outline-danger">
                      Recupera password
                    </Button>
                  </center>
                  <br />
                </AvForm>
              </Container>
            </Jumbotron>
          </div>
        </div>

        <FooterFixed />
      </div>
    );
  }
}
