import React, { Component } from "react";
import axios from "axios";
import history from "../../history";

import "bootstrap/dist/css/bootstrap.min.css";


function eseguiLogout(props) {
  props.preventDefault();

  axios
    .post(
      "http://localhost:3000/logoutAdmin",
      null,
      {
        params: {
          idAdmin: localStorage.getItem("id"),
        },
      },
      {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          Accept: "application/json",
        },
      }
    )
    .then((res) => {
      console.log(res);
      console.log(res.data);

      if (res.data.success === true) {
        alert("Logout eseguito con successo!");
        console.log(res.data);
        localStorage.removeItem("id");
        history.push("/admin");
        window.location.reload(false);
      } else {
        alert(res.data.message);
      }
    });
}

export default class Navbar extends Component {
  render() {
    return (
      
      <nav class="navbar navbar-expand-sm navbar-dark justify-content-end" style={{backgroundColor: '#dc3545'}}>
              <a class="navbar-brand glacialReg" href="/" style={{fontSize:'30px'}}>DCBooking</a>
              <button class="btn btn-light ml-auto mr-1 glacialReg" onClick={eseguiLogout}>Esci</button>
      </nav>
    );
  }
}
